import { createSlice, PayloadAction, createAsyncThunk } from "@reduxjs/toolkit";
import { AxiosResponse } from "axios";
import Swal from "sweetalert2";

import { instance } from "../../app/axiosInstance";
import { RootState } from "../../app/store";
import { IOrderInDetails } from "../../schemas";
import { startLoading, stopLoading, setError } from "../ui/uiSlice";
import { getMsgError } from "../../app/helpers";

type OrdersState = {
  success: boolean;
  subtotal: number;
  shippingPrice: number;
  taxPrice: number;
  total: number;
  currentOrder: IOrderInDetails | undefined;
  orders: IOrderInDetails[];
  loading: boolean;
  error: string | null;
};

const initialState: OrdersState = {
  success: false,
  subtotal: 0,
  shippingPrice: 0,
  taxPrice: 0,
  total: 0,
  currentOrder: undefined,
  orders: [],
  loading: false,
  error: null,
};

export const getOrderById = createAsyncThunk(
  "order/getOrderById",
  async (id: string, { dispatch }) => {
    const _id = Number(id);
    dispatch(startLoadingOrder());
    try {
      const response: AxiosResponse = await instance.get(`/api/orders/${_id}`);

      const order: IOrderInDetails = {
        _id: response.data._id,
        orderItems: response.data.orderItems,
        paymentMethod: response.data.paymentMethod,
        shippingAddress: response.data.shippingAddress,
        shippingPrice: response.data.shippingPrice,
        subTotal: response.data.subTotal,
        taxPrice: response.data.taxPrice,
        totalPrice: response.data.totalPrice,
        createdAt: response.data.createdAt,
        deleveredAt: response.data.deleveredAt,
        isDelivered: response.data.isDelivered,
        isPaid: response.data.isPaid,
        paidAt: response.data.paidAt,
        user: response.data.user,
      };
      dispatch(finishLoadingOrder());

      return order;
    } catch (err: any) {
      const msgError = getMsgError(err);
      dispatch(setError(msgError));
      dispatch(finishLoadingOrder());
      dispatch(setSuccess(false));
      Swal.fire("Error", msgError, "error");
      throw err;
    }
  }
);

interface payOrderProps {
  id: string;
  paymentResult: string;
}
export const payOrder2 = createAsyncThunk(
  "order/payOrder",
  async (props: payOrderProps, { dispatch }) => {
    const _id = Number(props.id);

    dispatch(startLoading());
    try {
      const response: AxiosResponse = await instance.put(
        `/api/orders/${_id}/pay`,
        props.paymentResult
      );
      dispatch(stopLoading());
    } catch (err: any) {
      dispatch(stopLoading());
      const msgError = getMsgError(err);
      dispatch(setOrderError(msgError));
      Swal.fire("Error", msgError, "error");
      throw err;
    }
  }
);

export const myOrderSlice = createSlice({
  name: "userOrders",
  initialState,
  reducers: {
    setCurrentOrder: (state, action: PayloadAction<IOrderInDetails>) => {
      state.currentOrder = action.payload;
    },
    removeCurrentOrder: (state) => {
      state.currentOrder = undefined;
    },
    setOrders: (state, action: PayloadAction<IOrderInDetails[]>) => {
      state.orders = action.payload;
    },
    resetOrders: (state) => {
      state.orders = [];
    },
    setSuccess: (state, action: PayloadAction<boolean>) => {
      state.success = action.payload;
    },
    setPrices: (
      state,
      action: PayloadAction<{
        subTotal: number;
        taxPrice: number;
        shippingPrice: number;
        total: number;
      }>
    ) => {
      state.subtotal = action.payload.subTotal;
      state.shippingPrice = action.payload.shippingPrice;
      state.taxPrice = action.payload.taxPrice;
      state.total = action.payload.total;
    },
    startLoadingOrder: (state) => {
      state.loading = true;
    },
    finishLoadingOrder: (state) => {
      state.loading = false;
    },
    setOrderError: (state, action: PayloadAction<string | null>) => {
      state.error = action.payload;
    },
    removeOrderError: (state) => {
      state.error = null;
    },
  },
  extraReducers: (builder) => {
    builder.addCase(
      getOrderById.fulfilled,
      (state, action: PayloadAction<IOrderInDetails>) => {
        state.currentOrder = action.payload;
      }
    );
  },
});

export const {
  setCurrentOrder,
  setOrders,
  setSuccess,
  setPrices,
  startLoadingOrder,
  finishLoadingOrder,
  setOrderError,
  removeOrderError,
  resetOrders,
  removeCurrentOrder,
} = myOrderSlice.actions;

export const getCurrentOrder = (state: RootState) =>
  state.userOrders.currentOrder;
export const getSuccess = (state: RootState) => state.userOrders.success;
export const getOrderState = (state: RootState) => state.userOrders;
export const isLoadingOrder = (state: RootState) => state.userOrders.loading;
export const getOrders = (state: RootState) => state.userOrders.orders;
export const getOrderError = (state: RootState) => state.userOrders.error;

export default myOrderSlice.reducer;
