import React, { useEffect } from "react";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { RouteComponentProps } from "react-router";

import { isLoading, isError } from "../features/ui/uiSlice";

import { getProductsState } from "../features/products/productSlice";
import { getProductsPerPage } from "../features/products/productAPI";
import { useAppSelector, useAppDispatch } from "../app/hooks";
import { Loading } from "../components/loading/loading";
import { Product } from "../features/products/Product";
import { Message } from "../components/Message";
import Paginate from "../components/Paginate";
import ProductSearchResults from "../components/products/ProductSearchResults";
import ProductCarousel from "../components/products/ProductCarousel";

export const HomeScreen: React.FC<RouteComponentProps> = ({ history }) => {
  const productsState = useAppSelector(getProductsState);

  const error = useAppSelector(isError);
  const loading = useAppSelector(isLoading);
  const dispatch = useAppDispatch();

  const {
    loading: loadingProducts,
    pagination,
    isSearchResults,
    currentProductsPerPage,
  } = productsState;

  const products = currentProductsPerPage
    ? currentProductsPerPage.products
    : [];

  useEffect(() => {
    if (products.length < 1 && !isSearchResults) {
      dispatch(getProductsPerPage(pagination.page));
    }
  }, [dispatch, products.length, isSearchResults, pagination.page]);

  if (loading) {
    return <Loading />;
  }
  if (error) {
    return <Message variant="danger">{error}</Message>;
  }

  const handlePaginationClick = (page: number) => {
    dispatch(getProductsPerPage(page));
  };

  return (
    <>
      {isSearchResults ? (
        <ProductSearchResults />
      ) : (
        <>
          <Row className="m-4">
            <h2 className="text-center">Top products</h2>
            <ProductCarousel />
          </Row>
          <Row>
            <h1>Latest Products</h1>
            {loadingProducts && <Loading />}
            {products.map((product) => (
              <Col sm={12} md={6} lg={4} xl={3} key={product._id}>
                <Product {...product} />
              </Col>
            ))}
            <Col sm={12}>
              <Paginate
                page={pagination.page}
                pages={pagination.pages}
                search={false}
                handleClick={handlePaginationClick}
              />
            </Col>
          </Row>
        </>
      )}
    </>
  );
};
