import React, { FC, useEffect } from "react";
import { Container } from "react-bootstrap";
import { Link } from "react-router-dom";
import { RouteComponentProps } from "react-router";
import { useAppDispatch, useAppSelector } from "../app/hooks";
// import { useForm } from "../app/hooks/useForm";
import ProductForm from "../features/products/ProductForm";
import { createProduct } from "../features/products/productAPI";
import {
  getProductsState,
  setReqSuccess,
} from "../features/products/productSlice";
import { LoadSpinner } from "../components/LoadSpinner";
import { IProduct, ICreateProduct } from "../schemas";

const ProductCreateScreen: FC<RouteComponentProps> = ({ history }) => {
  const dispatch = useAppDispatch();
  const { reqSuccess, loading } = useAppSelector(getProductsState);

  const submitHandler = (
    product: IProduct | ICreateProduct,
    imagesToLoad?: FileList
  ) => {
    dispatch(createProduct(product as ICreateProduct, imagesToLoad));
  };

  useEffect(() => {
    if (reqSuccess) {
      history.push("/admin/productList");
    }

    return () => {
      dispatch(setReqSuccess(false));
    };
  }, [reqSuccess, history, dispatch]);

  return (
    <Container>
      <Link to="/admin/productList">Go Back</Link>
      <h1>Create Product</h1>
      {loading && <LoadSpinner />}
      <ProductForm product={null} eventHandler={submitHandler} />
    </Container>
  );
};

export default ProductCreateScreen;
