import React, { VFC } from "react";
import { Spinner } from "react-bootstrap";

export const LoadSpinner: VFC = () => {
  return (
    <Spinner
      animation="border"
      variant="success"
      role="status"
      style={{
        height: "50px",
        width: "50px",
        margin: "auto",
        display: "inline-block",
      }}
    >
      <span className="sr-only">Loading...</span>
    </Spinner>
  );
};
