import React, { useState, useEffect } from "react";
import { Form, Button, Card, Row, Col } from "react-bootstrap";
import { useForm } from "../../app/hooks/useForm";
import ImageUpload from "../../components/ImageUpload";
import { IProduct, ICreateProduct, IImage } from "../../schemas";
import { setMainProductImage } from "../../app/helpers";
import { updateProducts } from "../products/productSlice";
import { useAppDispatch } from "../../app/hooks";

interface formProps {
  name: string;
  price: number;
  brand: string;
  category: string;
  countInStock: number;
  description: string;
}

interface Props {
  product: IProduct | null;
  eventHandler: (
    product: IProduct | ICreateProduct,
    images?: FileList,
    imagesToDelete?: IImage[]
  ) => void;
}

const ProductForm: React.VFC<Props> = ({ product, eventHandler }) => {
  const dispatch = useAppDispatch();
  const edit = product ? true : false;
  const [filesToUpload, setFilesToUpload] = useState<FileList | undefined>(
    undefined
  );

  const [imageToDelete, setImageToDelete] = useState<IImage[]>([]);
  const [productImages, setProductImages] = useState<IImage[]>([]);

  useEffect(() => {
    if (product && product.images) {
      setProductImages(product.images);
    }
  }, [product]);

  const [formValues, actions] = useForm<formProps>({
    name: product?.name || "",
    price: product?.price || 0,
    brand: product?.brand || "",
    category: product?.category || "",
    countInStock: product?.countInStock || 0,
    description: product?.description || "",
  });

  const {
    values: { name, price, brand, category, countInStock, description },
  } = formValues;

  const { handleInputChange } = actions;

  const submitHandler = (e: React.FormEvent) => {
    e.preventDefault();

    // create  product
    if (!product) {
      const newProduct: ICreateProduct = {
        name,
        price,
        brand,
        category,
        countInStock,
        description,
        image: "testImage",
      };
      eventHandler(newProduct, filesToUpload);
    } else {
      const updatedProduct: IProduct = {
        _id: product._id,
        name,
        price,
        brand,
        category,
        countInStock,
        description,
        rating: product.rating,
        numReviews: product.numReviews,
        mainImage: product.mainImage,
        mainImageUrl: product.mainImageUrl,
        images: product.images,
      };
      eventHandler(updatedProduct, filesToUpload, imageToDelete);
    }
  };

  const onImageDeleteHandler = (image: IImage) => {
    const updatedImagesToDelete = imageToDelete;
    updatedImagesToDelete.push(image);
    setImageToDelete(updatedImagesToDelete);

    if (product && product.images) {
      const updatedProductImages = productImages.filter(
        (img) => Number(img._id) !== Number(image._id)
      );
      setProductImages(updatedProductImages);
    }
  };

  const onSetMainImage = async (image: IImage) => {
    const productId = product?._id ? product._id : "-1";

    await setMainProductImage(image._id, productId);
    const updatedProdImgs = productImages.map((img) => {
      if (img.url === image.url) {
        return {
          _id: image._id,
          publicId: image.publicId,
          url: image.url,
          isMain: true,
        };
      }
      return {
        _id: img._id,
        publicId: img.publicId,
        url: img.url,
        isMain: false,
      };
    });

    setProductImages(updatedProdImgs);
    dispatch(updateProducts(productId, updatedProdImgs));
  };

  return (
    <Form onSubmit={submitHandler}>
      <Form.Group controlId="name" className="my-3">
        <Form.Label>Name</Form.Label>
        <Form.Control
          type="text"
          placeholder="Enter product name"
          name="name"
          value={name}
          onChange={handleInputChange}
        ></Form.Control>
      </Form.Group>
      <Form.Group controlId="price" className="my-3">
        <Form.Label>Price</Form.Label>
        <Form.Control
          type="number"
          placeholder="Enter Price"
          name="price"
          value={price}
          onChange={handleInputChange}
        ></Form.Control>
      </Form.Group>
      {edit ? (
        productImages.length > 0 ? (
          <Row className="my-4">
            <Col xs={12}>
              <Form.Label>Product Images</Form.Label>
            </Col>
            {productImages.map((image) => {
              return (
                <Col md={3} key={image.url}>
                  <Card>
                    <Card.Img variant="top" src={image.url} />
                    <Button
                      variant="danger"
                      disabled={image.isMain ? true : false}
                      onClick={() => onImageDeleteHandler(image)}
                    >
                      delete
                    </Button>
                    <Button
                      variant="primary"
                      onClick={() => onSetMainImage(image)}
                      disabled={image.isMain ? true : false}
                    >
                      Set as Main Image
                    </Button>
                  </Card>
                </Col>
              );
            })}
          </Row>
        ) : null
      ) : null}
      <Form.Group controlId="image" className="my-3">
        <Form.Label>Upload Images</Form.Label> <br />
        <ImageUpload fileList={filesToUpload} setFileList={setFilesToUpload} />
      </Form.Group>
      <Form.Group controlId="brand" className="my-3">
        <Form.Label>Brand</Form.Label>
        <Form.Control
          type="text"
          placeholder="Enter Brand"
          name="brand"
          value={brand}
          onChange={handleInputChange}
        ></Form.Control>
      </Form.Group>
      <Form.Group controlId="category" className="my-3">
        <Form.Label>Category</Form.Label>
        <Form.Control
          type="text"
          placeholder="Enter Category"
          name="category"
          value={category}
          onChange={handleInputChange}
        ></Form.Control>
      </Form.Group>
      <Form.Group controlId="countInStock" className="my-3">
        <Form.Label>Stock</Form.Label>
        <Form.Control
          type="number"
          placeholder="Enter countInStock"
          name="countInStock"
          value={countInStock}
          onChange={handleInputChange}
        ></Form.Control>
      </Form.Group>
      <Form.Group controlId="description" className="my-3">
        <Form.Label>Description</Form.Label>
        <Form.Control
          type="text"
          placeholder="Enter countInStock"
          name="description"
          value={description}
          onChange={handleInputChange}
        ></Form.Control>
      </Form.Group>
      <Button type="submit" variant="primary" className="mt-2">
        {product?.name ? "Update" : "Create"}
      </Button>
    </Form>
  );
};

export default ProductForm;
