import React, { useEffect, FC } from "react";
import { LinkContainer } from "react-router-bootstrap";
import { Table, Button } from "react-bootstrap";
import { RouteComponentProps } from "react-router";

import { useAppDispatch, useAppSelector } from "../app/hooks";
import { Loading } from "../components/loading/loading";
import { Message } from "../components/Message";
import { getUsersState } from "../features/Users/UsersSlice";
import { fetchAllUsers, userDelete } from "../features/Users/UsersAPI";
import { setSelectedUserById } from "../features/Users/UsersSlice";

export const UserListScreen: FC<RouteComponentProps> = ({ history }) => {
  const dispatch = useAppDispatch();
  const usersState = useAppSelector(getUsersState);

  const { loading, error, users } = usersState;

  useEffect(() => {
    dispatch(fetchAllUsers());
  }, [dispatch]);

  const deleteUserHandler = (id: string) => {
    if (window.confirm("Are you sure you want to delete this user?")) {
      dispatch(userDelete(id));
    }
  };

  const updateSelectedUser = (userId: string) => {
    dispatch(setSelectedUserById(userId));
  };

  return (
    <div>
      <h1>Users</h1>
      {loading && <Loading />}
      {error && <Message variant="danger">{error}</Message>}
      {users.length > 0 ? (
        <Table striped bordered hover responsive className="table-sm">
          <thead>
            <tr>
              <th>ID</th>
              <th>NAME</th>
              <th>EMAIL</th>
              <th>ADMIN</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {users.map((user) => (
              <tr key={user._id}>
                <td>{user._id}</td>
                <td>{user.name}</td>
                <td>{user.email}</td>
                <td>
                  {user.isAdmin ? (
                    <i className="fas fa-check" style={{ color: "green" }}></i>
                  ) : (
                    <i className="fas fa-check" style={{ color: "red" }}></i>
                  )}
                </td>

                <td>
                  <LinkContainer to={`/admin/user/${user._id}/edit`}>
                    <Button
                      variant="light"
                      className="btn-sm"
                      onClick={() => updateSelectedUser(user._id)}
                    >
                      <i className="fas fa-edit"></i>
                    </Button>
                  </LinkContainer>
                  <Button
                    variant="danger"
                    className="btn-sm"
                    onClick={() => deleteUserHandler(user._id)}
                  >
                    <i className="fas fa-trash"></i>
                  </Button>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
      ) : undefined}
    </div>
  );
};
