import React, { FC, useEffect } from "react";
import { Link } from "react-router-dom";
import { RouteComponentProps } from "react-router";
import { Form, Button, Row, Col, Container } from "react-bootstrap";

import { useAppDispatch, useAppSelector } from "../app/hooks";
import { useForm } from "../app/hooks/useForm";
import { Message } from "../components/Message";
// import { Loading } from "../components/loading/loading";
import { isChecking, getUser } from "../features/Auth/authSlice";
import { isError, removeError } from "../features/ui/uiSlice";
import { startLogin } from "../features/Auth/authAPI";
import { LoadSpinner } from "../components/LoadSpinner";

export const LoginScreen: FC<RouteComponentProps> = ({ location, history }) => {
  const dispatch = useAppDispatch();
  const checking = useAppSelector(isChecking);
  const error = useAppSelector(isError);
  const user = useAppSelector(getUser);

  const redirect = location.search ? location.search.split("=")[1] : "/";

  interface formProps {
    email: string;
    password: string;
  }

  const [formValues, actions] = useForm<formProps>({
    email: "",
    password: "",
  });

  const {
    values: { email, password },
  } = formValues;
  const { handleInputChange, reset } = actions;

  const submitHandler = (e: React.FormEvent) => {
    e.preventDefault();

    dispatch(startLogin(email, password));

    if (!!error && !!checking) {
      reset();
    }
  };

  useEffect(() => {
    return () => {
      dispatch(removeError());
    };
  }, [dispatch]);

  useEffect(() => {
    if (user) {
      history.push(redirect);
    }
  }, [history, user, redirect]);

  return (
    <Container>
      <Row className="justify-content-md-center">
        <Col xs={12} md={6}>
          <h1>Sign In</h1>
          {error && <Message variant="danger">{error}</Message>}
          <Form onSubmit={submitHandler}>
            <Form.Group controlId="email">
              <Form.Label>Email Address</Form.Label>
              <Form.Control
                type="email"
                placeholder="Enter Email"
                name="email"
                value={email}
                onChange={handleInputChange}
              ></Form.Control>
            </Form.Group>

            <Form.Group controlId="password">
              <Form.Label>Password</Form.Label>
              <Form.Control
                type="password"
                placeholder="Enter Password"
                name="password"
                value={password}
                onChange={handleInputChange}
              ></Form.Control>
            </Form.Group>

            <Button
              type="submit"
              variant="primary"
              className="mt-2"
              disabled={checking}
            >
              Sign In
            </Button>
            {checking ? <LoadSpinner /> : undefined}
          </Form>

          <Row className="py-3">
            <Col>
              <p>
                New Customer?
                <Link
                  to={redirect ? `/register?redirect=${redirect}` : "/register"}
                >
                  Register
                </Link>
              </p>
            </Col>
          </Row>
        </Col>
      </Row>
    </Container>
  );
};
