import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { RootState } from "../../app/store";

interface uiState {
  loading: Boolean;
  msgError: string | null;
}

const initialState: uiState = {
  loading: false,
  msgError: null,
};

export const uiSlice = createSlice({
  name: "ui",
  initialState,
  reducers: {
    setError: (state, action: PayloadAction<string>) => {
      state.msgError = action.payload;
      state.loading = false;
    },
    removeError: (state) => {
      state.msgError = null;
    },
    startLoading: (state) => {
      state.loading = true;
    },
    stopLoading: (state) => {
      state.loading = false;
    },
  },
});

export const { setError, removeError, startLoading, stopLoading } =
  uiSlice.actions;

export const isLoading = (state: RootState) => state.ui.loading;

export const isError = (state: RootState) => state.ui.msgError;

export default uiSlice.reducer;
