import { createSlice, PayloadAction } from "@reduxjs/toolkit";

import { RootState } from "../../app/store";
import { IOrderInDetails } from "../../schemas";

type OrdersState = {
  isLoading: boolean;
  orders: IOrderInDetails[];
};

const initialState: OrdersState = {
  isLoading: false,
  orders: [],
};

export const OrdersSlice = createSlice({
  name: "orderList",
  initialState,
  reducers: {
    setOrders: (state, action: PayloadAction<IOrderInDetails[]>) => {
      state.orders = action.payload;
    },
    setLoading: (state, action: PayloadAction<boolean>) => {
      state.isLoading = action.payload;
    },
  },
});

export const { setLoading, setOrders } = OrdersSlice.actions;

export const getOrdersState = (state: RootState) => state.orders;

export default OrdersSlice.reducer;
