import React, { useEffect, useState, useRef, useCallback } from "react";
import { Row, Col, Button } from "react-bootstrap";
//import {resizeFile} from '../app/helpers';

interface Props {
  fileList: FileList | undefined;
  setFileList: React.Dispatch<React.SetStateAction<FileList | undefined>>;
}

const UploadImages: React.VFC<Props> = ({ fileList, setFileList }) => {
  // const [fileList, setFileList] = useState<FileList | null>(null);
  const [imageURLs, setImageURLs] = useState<string[]>([]);

  const inputRef = useRef<HTMLInputElement>(null);

  const fileToBlob = async (file: File) =>
    new Blob([new Uint8Array(await file.arrayBuffer())], { type: file.type });

  const updateImageUrlsCB2 = useCallback(async () => {
    const updatedImgUrls: string[] = [];

    if (fileList) {
      for (let i = 0; i < fileList.length; i++) {
        const fileImage = fileList.item(i);
        if (fileImage) {
          //const resizedImg = await resizeFile(fileImage);
          const blobImage = await fileToBlob(fileImage);
          updatedImgUrls.push(URL.createObjectURL(blobImage));
        }
      }
    }
    setImageURLs(updatedImgUrls);
  }, [fileList]);

  useEffect(() => {
    if (fileList && fileList.length < 1) return;
    updateImageUrlsCB2();
  }, [fileList, updateImageUrlsCB2]);

  const onImageChange = async (e: FileList | null) => {
    if (e) {
      const fileArray = [];
      for (let i = 0; i < e.length; i++) {
        const imgFile = e.item(i);
        if (imgFile) {
          fileArray.push(await fileToBlob(imgFile));
        }
      }

      setFileList(e);
    }
  };

  const deleteFile = (url: string, idx: number) => {
    const updatedImageUrls = imageURLs.filter((i) => i !== url);
    setImageURLs(updatedImageUrls);

    if (fileList) {
      const dt = new DataTransfer();

      for (let i = 0; i < fileList?.length; i++) {
        const file = fileList[i];
        if (idx !== i) {
          dt.items.add(file);
        }
      }

      inputRef!.current!.files = dt.files;
      setFileList(dt.files);
    }
  };

  return (
    <>
      <input
        type="file"
        multiple
        accept="image/*"
        ref={inputRef}
        onChange={(e) => onImageChange(e.target.files)}
      />
      <Row>
        {imageURLs.map((imgSrc, idx) => (
          <Col xs={12} md={3} key={imgSrc}>
            <img src={imgSrc} width="200px" alt={`to upload ${idx}`} />
            <Button variant="danger" onClick={() => deleteFile(imgSrc, idx)}>
              Eliminar
            </Button>
          </Col>
        ))}
      </Row>
    </>
  );
};

export default UploadImages;
