import React, { VFC, useState, useEffect } from "react";
import { ListGroup, Button, Form } from "react-bootstrap";
import { Link } from "react-router-dom";
import { useAppDispatch, useAppSelector } from "../../app/hooks";
import { getAuthState } from "../../features/Auth/authSlice";
import { useForm } from "../../app/hooks/useForm";
import {
  getLoadingReview,
  getCreateReviewSuccess,
} from "../../features/Reviews/ReviewsSlice";
import { Loading } from "../loading/loading";
import { Message } from "../Message";
import { createProductReview } from "../../features/Reviews/ReviewsAPI";

interface formProps {
  rating: string;
  comment: string;
}

interface Props {
  productId: string;
}

export const WriteReview: VFC<Props> = ({ productId }) => {
  const dispatch = useAppDispatch();
  const userState = useAppSelector(getAuthState);
  const isProductReviewLoading = useAppSelector(getLoadingReview);
  const reviewSuccess = useAppSelector(getCreateReviewSuccess);

  const { user } = userState;

  const [formValues, actions] = useForm<formProps>({
    rating: "",
    comment: "",
  });

  const {
    values: { rating, comment },
  } = formValues;
  const { handleInputChange, reset } = actions;

  useEffect(() => {
    if (reviewSuccess) {
      reset();
    }
  }, [reviewSuccess, reset]);

  const submitHandler = (e: React.FormEvent) => {
    e.preventDefault();
    dispatch(createProductReview(productId, { rating, comment }));
  };

  return (
    <>
      <h4>Write a Review</h4>
      {isProductReviewLoading && <Loading />}
      <ListGroup>
        <ListGroup.Item>
          {user ? (
            <Form onSubmit={submitHandler}>
              <Form.Group controlId="rating">
                <Form.Label>Rating</Form.Label>
                <Form.Control
                  as="select"
                  value={rating}
                  name="rating"
                  onChange={handleInputChange}
                >
                  <option value="">Select ...</option>
                  <option value="1">1 - Poor</option>
                  <option value="2">2 - Fair</option>
                  <option value="3">3 - Good</option>
                  <option value="4">4 - Very Good</option>
                  <option value="5">5 - Excellent</option>
                </Form.Control>
              </Form.Group>
              <Form.Group controlId="comment">
                <Form.Label>Review</Form.Label>
                <Form.Control
                  as="textarea"
                  name="comment"
                  value={comment}
                  onChange={handleInputChange}
                ></Form.Control>
              </Form.Group>
              <Button
                type="submit"
                variant="primary"
                disabled={isProductReviewLoading}
                className="mt-3"
              >
                Submit
              </Button>
            </Form>
          ) : (
            <Message variant="info">
              Please <Link to="/login">login</Link> to write a review
            </Message>
          )}
        </ListGroup.Item>
      </ListGroup>
    </>
  );
};
