import React from "react";
import { useAppDispatch } from "../../app/hooks";

import { Button, Form } from "react-bootstrap";
import { useHistory } from "react-router-dom";
import { useForm } from "../../app/hooks/useForm";
import { getProductsSearch } from "../../features/products/productAPI";
import { setkeyword } from "../../features/products/productSlice";

interface formProps {
  keyword: string;
}

const SearchBox = () => {
  let history = useHistory();
  const dispatch = useAppDispatch();

  const [formValues, actions] = useForm<formProps>({
    keyword: "",
  });

  const {
    values: { keyword },
  } = formValues;

  const { handleInputChange, reset } = actions;

  const submitHandler = (e: React.FormEvent) => {
    e.preventDefault();
    if (keyword.trim().length > 0) {
      dispatch(setkeyword(keyword));
      dispatch(getProductsSearch(1));
      reset();
      history.push("/");
    } else {
      history.push(history.location.pathname);
    }
  };

  return (
    <Form onSubmit={submitHandler} className="d-flex mr-2">
      <Form.Control
        type="text"
        name="keyword"
        value={keyword}
        onChange={handleInputChange}
        className="mr-sm-2 ml-sm-5"
      ></Form.Control>
      <Button type="submit" variant="outline-success" className="p-2 ml-2">
        Search
      </Button>
    </Form>
  );
};

export default SearchBox;
