import { AxiosResponse } from "axios";
import Swal from "sweetalert2";

import { instance } from "../../app/axiosInstance";
import { AppThunk } from "../../app/store";

import { setLoading, setOrders } from "./OrdersSlice";
import { getMsgError } from "../../app/helpers";
import { IOrderInDetails } from "../../schemas";
import { setCurrentOrder } from "../MyOrders/MyOrdersSlice";

export const fetchAllOrders = (): AppThunk => async (dispatch) => {
  dispatch(setLoading(true));

  try {
    const response: AxiosResponse = await instance.get("/api/orders/");
    const orders: IOrderInDetails[] = response.data;

    dispatch(setOrders(orders));
    dispatch(setLoading(false));
  } catch (err: any) {
    dispatch(setLoading(false));
    const msgError = getMsgError(err);
    Swal.fire("Error", msgError, "error");
  }
};

export const getAllOrders = (): AppThunk => async (dispatch, getState) => {
  const orders = getState().orders.orders;
  if (orders.length > 0) return;
  dispatch(fetchAllOrders());
};

export const deliverOrder =
  (orderId: string): AppThunk =>
  async (dispatch, getState) => {
    const orders = getState().orders.orders;

    const localOrder = orders.find(
      (order) => Number(order._id) === Number(orderId)
    );

    if (!localOrder) {
      Swal.fire(
        "Error",
        `ORDEN CON ID: ${orderId} NO ENCONTRADO EN MEMORIA`,
        "error"
      );
      return;
    }

    dispatch(setLoading(true));
    try {
      const response: AxiosResponse = await instance.put(
        `/api/orders/${orderId}/deliver/`,
        {}
      );

      const updatedOrder = {
        ...localOrder,
        isDelivered: true,
        deleveredAt: response.data.deliveredAt,
      };

      const updatedOrders = orders.map((order) => {
        if (Number(order._id) === Number(orderId)) {
          return updatedOrder;
        }

        return order;
      });
      dispatch(setCurrentOrder(updatedOrder));
      dispatch(setOrders(updatedOrders));
      dispatch(setLoading(false));
    } catch (err: any) {
      dispatch(setLoading(false));
      const msgError = getMsgError(err);
      Swal.fire("Error", msgError, "error");
    }
  };
