import React from "react";
import { useHistory } from "react-router-dom";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";
import { useAppSelector, useAppDispatch } from "../../app/hooks";
import {
  getProductsState,
  setIsSearchResults,
} from "../../features/products/productSlice";
import { getProductsSearch } from "../../features/products/productAPI";
import { Product } from "../../features/products/Product";
import Paginate from "../Paginate";

const ProductSearchResults: React.FC = () => {
  const productsState = useAppSelector(getProductsState);
  const dispatch = useAppDispatch();
  let history = useHistory();

  const {
    currentSearchResults,
    searchPagination: pagination,
    keyword,
  } = productsState;

  const products = currentSearchResults?.products
    ? currentSearchResults.products
    : [];

  const handlePaginationClick = (page: number) => {
    dispatch(getProductsSearch(page));
  };

  const resetSearchHandler = () => {
    dispatch(setIsSearchResults(false));
    history.push("/");
  };

  return (
    <Row>
      <Col sm={12}>
        <h1>Search results for {keyword}</h1>
      </Col>
      <Col sm={12}>
        <Button variant="warning" onClick={resetSearchHandler}>
          Clear search results
        </Button>
      </Col>
      {products.map((prod) => (
        <Col sm={12} md={6} lg={4} xl={3} key={prod._id}>
          <Product {...prod} />
        </Col>
      ))}
      <Col sm={12}>
        <Paginate
          page={pagination.page}
          pages={pagination.pages}
          search={true}
          handleClick={handlePaginationClick}
        />
      </Col>
    </Row>
  );
};

export default ProductSearchResults;
