import { AxiosError, AxiosResponse } from "axios";
import Swal from "sweetalert2";
import Resizer from "react-image-file-resizer";
import { instanceImageUpload, instance } from "../app/axiosInstance";
import { IImage } from "../schemas";

export const saveToLocalStorage = (name: string, state: any) => {
  try {
    localStorage.setItem(name, JSON.stringify(state));
  } catch (e) {
    console.error("ERROR AL GUARDAR EN LOCALSTORAGE");
    console.error(e);
  }
};

export const loadFromLocalStorage = (name: string) => {
  try {
    const stateStr = localStorage.getItem(name);
    return stateStr ? JSON.parse(stateStr) : undefined;
  } catch (e) {
    console.error("ERROR AL CARGAR DE LOCALSTORAGE");
    console.error(e);
    return undefined;
  }
};

export const getMsgError = (err: AxiosError) => {
  let msgError = "Connection to server fail, please try again later!";

  if (err.response && err.response.data.message) {
    msgError = err.response.data.message;
  } else {
    msgError = err.message;
  }
  return msgError;
};

export const productImageUpload = async (
  images: FileList,
  productId: string
) => {
  try {
    const formData = new FormData();

    for (let i = 0; i < images.length; i++) {
      formData.append("images", images[i]);
    }
    formData.append("product_id", productId);

    const response: AxiosResponse = await instanceImageUpload.post(
      "/api/products/upload/",
      formData
    );

    return response.data;
  } catch (error: any) {
    const msgError = getMsgError(error);
    Swal.fire("Error", msgError, "error");
  }
};

export const deleteImage = async (imagesToDelete: IImage[]) => {
  try {
    const response: AxiosResponse = await instance.put(
      "/api/products/image/delete/",
      { imagesToDelete: imagesToDelete }
    );
    return response;
  } catch (err: any) {
    const msgError = getMsgError(err);
    Swal.fire("Error", msgError, "error");
  }
};

export const setMainProductImage = async (
  imageId: string,
  productId: string
) => {
  try {
    const response = await instance.post("/api/products/image/setMain/", {
      image_id: imageId,
      product_id: productId,
    });
    return response;
  } catch (err: any) {
    Swal.fire("Error", "failed to update main image", "error");
  }
};

export const resizeFile = (file: File) => {
  new Promise((resolve) => {
    Resizer.imageFileResizer(
      file,
      900, // maxWidth
      900, // maxHeight
      "JPEG", // Compress format
      80, // Quality
      0, // Rotation
      (uri) => {
        // Response UriFunction
        // Is the Callback fn of the resized new image URI
        resolve(uri);
      },
      "base64" // Is the output type of the resized new image
    );
  });
};
