import React, { VFC, useEffect } from "react";
import { Link, RouteComponentProps } from "react-router-dom";

import { useAppDispatch, useAppSelector } from "../app/hooks";

import {
  getSelectedProduct,
  setSelectedProductById,
} from "../features/products/productSlice";
import { isLoading, isError } from "../features/ui/uiSlice";

import { ProductDetails } from "../features/products/productDetails";
import { Loading } from "../components/loading/loading";
import { Message } from "../components/Message";

type TParams = { id: string };

export const ProductScreen: VFC<any> = ({
  match,
}: RouteComponentProps<TParams>) => {
  const dispatch = useAppDispatch();
  const product = useAppSelector(getSelectedProduct);
  let loading = useAppSelector(isLoading);
  let error = useAppSelector(isError);
  const match_id = match.params.id;

  useEffect(() => {
    if (!product && !loading) {
      dispatch(setSelectedProductById(match_id));
    }
  }, [product, dispatch, match_id, loading]);

  if (loading) {
    return <Loading />;
  }

  return (
    <div>
      <Link to="/" className="btn btn-light my-2">
        Go Back
      </Link>
      {product ? (
        <ProductDetails {...product} />
      ) : (
        <Message variant="danger">{error}</Message>
      )}
    </div>
  );
};
