import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { RootState } from "../../app/store";
import { IProductReview } from "../../schemas";

export type ProductReviewState = {
  productId: string;
  productReviews: IProductReview[];
};

type ReviewsState = {
  loading: boolean;
  loadingReview: boolean;
  createReviewSuccess: boolean;
  selectedProductReviews: IProductReview[] | undefined;
  reviews: ProductReviewState[];
};

const initialState: ReviewsState = {
  loading: false,
  loadingReview: false,
  createReviewSuccess: false,
  selectedProductReviews: undefined,
  reviews: [],
};

export const reviewsSlice = createSlice({
  name: "reviews",
  initialState,
  reducers: {
    setLoading: (state, action: PayloadAction<boolean>) => {
      state.loading = action.payload;
    },
    setLoadingReview: (state, action: PayloadAction<boolean>) => {
      state.loading = action.payload;
    },
    setCreateReviewSuccess: (state, action: PayloadAction<boolean>) => {
      state.createReviewSuccess = action.payload;
    },
    setReviews: (state, action: PayloadAction<ProductReviewState[]>) => {
      state.reviews = action.payload;
    },
    setSelectedProductReviews: (
      state,
      action: PayloadAction<IProductReview[] | undefined>
    ) => {
      state.selectedProductReviews = action.payload;
    },
  },
});

export const {
  setLoading,
  setReviews,
  setSelectedProductReviews,
  setLoadingReview,
  setCreateReviewSuccess,
} = reviewsSlice.actions;

export const getReviewsState = (state: RootState) => state.reviews.reviews;
export const getLoadingReviews = (state: RootState) => state.reviews.loading;
export const getSelectedProductReviews = (state: RootState) =>
  state.reviews.selectedProductReviews;

export const getLoadingReview = (state: RootState) =>
  state.reviews.loadingReview;

export const getCreateReviewSuccess = (state: RootState) =>
  state.reviews.createReviewSuccess;

export default reviewsSlice.reducer;
