import { createSlice, PayloadAction, createAsyncThunk } from "@reduxjs/toolkit";

import { AppThunk, RootState } from "../../app/store";
import { ICartItem, IShippingAddress } from "../../schemas";
import { saveToLocalStorage } from "../../app/helpers";

export interface ICartState {
  items: ICartItem[];
  subtotal: number;
  shippingAddress: IShippingAddress | undefined;
  totalItems: number;
  paymentMethod: string;
}

interface IItems {
  items: ICartItem[];
  subtotal: number;
  totalItems: number;
}

const initialState: ICartState = {
  items: [],
  subtotal: 0,
  shippingAddress: undefined,
  totalItems: 0,
  paymentMethod: "",
};

const calcSubTotal = (items: ICartItem[]) => {
  let total = 0;
  items.forEach((cartItem) => {
    total += cartItem.product.price * cartItem.qty;
  });

  const updatedTotal = +total.toFixed(2);

  return updatedTotal;
};

const calcTotalItems = (items: ICartItem[]) => {
  const totalItems = items.reduce((acc, item) => acc + item.qty, 0);
  return totalItems;
};

export const addToCartLS =
  (item: ICartItem): AppThunk =>
  (dispatch, getState) => {
    const { items } = getState().cart;
    const itemInCart = items.find((i) => i.product._id === item.product._id);

    let updatedItems = [];
    if (itemInCart) {
      updatedItems = items.map((citem) => {
        if (citem.product._id === itemInCart.product._id) {
          item.qty += citem.qty;
          return item;
        }
        return citem;
      });
    } else {
      updatedItems = [...items, item];
    }
    const updatedSubTotal = calcSubTotal(updatedItems);
    const updatedTotalItems = calcTotalItems(updatedItems);

    localStorage.setItem(
      "cartItems",
      JSON.stringify({
        items: updatedItems,
        subtotal: updatedSubTotal,
        totalItems: updatedTotalItems,
      })
    );
    dispatch(
      updateItems({
        items: updatedItems,
        subtotal: updatedSubTotal,
        totalItems: updatedTotalItems,
      })
    );
  };

export const updateCartLS =
  (item: ICartItem): AppThunk =>
  (dispatch, getState) => {
    const { items } = getState().cart;
    const itemInCart = items.find((i) => i.product._id === item.product._id);

    let updatedItems = [];
    if (itemInCart) {
      updatedItems = items.map((citem) =>
        citem.product._id === itemInCart.product._id ? item : citem
      );
    } else {
      updatedItems = [...items, item];
    }
    const updatedSubTotal = calcSubTotal(updatedItems);
    const updatedTotalItems = calcTotalItems(updatedItems);

    localStorage.setItem(
      "cartItems",
      JSON.stringify({
        items: updatedItems,
        subtotal: updatedSubTotal,
        totalItems: updatedTotalItems,
      })
    );
    dispatch(
      updateItems({
        items: updatedItems,
        subtotal: updatedSubTotal,
        totalItems: updatedTotalItems,
      })
    );
  };

export const removeFromCartLS =
  (id: string): AppThunk =>
  (dispatch, getState) => {
    const { items } = getState().cart;
    const updatedItems = items.filter((i) => i.product._id !== id);
    const updatedSubTotal = calcSubTotal(updatedItems);
    const updatedTotalItems = calcTotalItems(updatedItems);
    localStorage.setItem(
      "cartItems",
      JSON.stringify({
        items: updatedItems,
        subtotal: updatedSubTotal,
        totalItems: updatedTotalItems,
      })
    );
    dispatch(
      updateItems({
        items: updatedItems,
        subtotal: updatedSubTotal,
        totalItems: updatedTotalItems,
      })
    );
  };

export const updateShippingAddressLS = createAsyncThunk(
  "cart/shippingAddressLS",
  (shippingAddress: IShippingAddress) => {
    saveToLocalStorage("shippingAddress", shippingAddress);

    return shippingAddress;
  }
);

export const savePaymentMethodLS = createAsyncThunk(
  "cart/paymentMethodLS",
  (pMethod: string) => {
    saveToLocalStorage("paymentMethod", pMethod);
    return pMethod;
  }
);

export const resetCartLS = createAsyncThunk("cart/reset", () => {
  localStorage.removeItem("cartItems");
});

export const cartSlice = createSlice({
  name: "cart",
  initialState,
  reducers: {
    updateItems: (state, action: PayloadAction<IItems>) => {
      state.items = action.payload.items;
      state.subtotal = action.payload.subtotal;
      state.totalItems = action.payload.totalItems;
    },
    updateShippingAddress: (state, action: PayloadAction<IShippingAddress>) => {
      state.shippingAddress = action.payload;
    },
    updatePaymentMethod: (state, action: PayloadAction<string>) => {
      state.paymentMethod = action.payload;
    },
    resetCart: (state) => {
      state.items = [];
      state.paymentMethod = "";
      state.shippingAddress = undefined;
      state.subtotal = 0;
      state.totalItems = 0;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(
        updateShippingAddressLS.fulfilled,
        (state, action: PayloadAction<IShippingAddress>) => {
          state.shippingAddress = action.payload;
        }
      )
      .addCase(
        savePaymentMethodLS.fulfilled,
        (state, action: PayloadAction<string>) => {
          state.paymentMethod = action.payload;
        }
      )
      .addCase(resetCartLS.fulfilled, (state) => {
        state.items = [];
        state.paymentMethod = "";
        state.shippingAddress = undefined;
        state.subtotal = 0;
        state.totalItems = 0;
      });
  },
});

export const {
  updateItems,
  updateShippingAddress,
  updatePaymentMethod,
  resetCart,
} = cartSlice.actions;

export const getCartItems = (state: RootState) => state.cart.items;
export const getSubtotal = (state: RootState) => state.cart.subtotal;
export const getTotalItems = (state: RootState) => state.cart.totalItems;
export const getShippingAddress = (state: RootState) =>
  state.cart.shippingAddress;

export const getCart = (state: RootState) => state.cart;

export default cartSlice.reducer;
