import React, { useEffect } from "react";
import { useAppSelector, useAppDispatch } from "../../app/hooks";
import Col from "react-bootstrap/Col";
import Carousel from "react-bootstrap/Carousel";
import { Link } from "react-router-dom";
import Image from "react-bootstrap/Image";

import { Loading } from "../loading/loading";
import { fetchTopProducts } from "../../features/products/productAPI";
import {
  getProductsState,
  setSelectedProduct,
} from "../../features/products/productSlice";
import { IProduct } from "../../schemas";

const ProductCarousel = () => {
  //    const topProducts = useAppSelector(getTopProducts);
  const productsState = useAppSelector(getProductsState);
  const dispatch = useAppDispatch();

  const { loading, topProducts } = productsState;

  useEffect(() => {
    if (topProducts.length < 1) {
      dispatch(fetchTopProducts());
    }
  }, [topProducts.length, dispatch]);

  const onClickHandler = (product: IProduct) => {
    dispatch(setSelectedProduct(product));
  };

  return (
    <Col md={{ span: 6, offset: 3 }}>
      {loading && <Loading />}
      <Carousel pause="hover">
        {topProducts.map((product) => (
          <Carousel.Item key={product._id}>
            <Link
              to={`/product/${product._id}`}
              onClick={() => onClickHandler(product)}
            >
              <Image
                src={product.mainImageUrl}
                alt={product.name}
                className="img-fluid carousel-image"
              />
              <Carousel.Caption>
                <h4>
                  {product.name} ($ {product.price})
                </h4>
              </Carousel.Caption>
            </Link>
          </Carousel.Item>
        ))}
      </Carousel>
    </Col>
  );
};

export default ProductCarousel;
