import React, { VFC, useState, useEffect, useCallback } from "react";
import { ListGroup, Row, Col, Image, Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import { ICartItem, IProduct } from "../../schemas";
import { useAppDispatch } from "../../app/hooks";
import { removeFromCartLS, updateCartLS } from "../Cart/cartSlice";
import { CountInStock2 } from "../products/CountInStock2";

interface Props {
  items: ICartItem[];
}

export const CartItems: VFC<Props> = ({ items }) => {
  const [qty, setQty] = useState<number | undefined>(undefined);
  const [cartProduct, setCartProduct] = useState<IProduct | undefined>(
    undefined
  );

  const dispatch = useAppDispatch();

  const updateQty = (e: React.ChangeEvent<HTMLInputElement>) => {
    const newValue = e.target.value;
    setQty(Number(newValue));
  };

  const updateProduct = (product: IProduct) => {
    setCartProduct(product);
  };

  const removeProductFromCart = (id: string) => {
    dispatch(removeFromCartLS(id));
  };

  const addToCartHandlerCb = useCallback(() => {
    const addToCartHandler = (product: IProduct | undefined) => {
      const quantity = qty || 1;
      if (product) {
        const item = {
          product,
          qty: quantity,
        };
        dispatch(updateCartLS(item));
      }
    };
    addToCartHandler(cartProduct);
  }, [cartProduct, dispatch, qty]);

  useEffect(() => {
    addToCartHandlerCb();
  }, [addToCartHandlerCb]);

  return (
    <ListGroup variant="flush">
      {items.map((item) => (
        <ListGroup.Item key={item.product._id}>
          <Row>
            <Col xs={2}>
              <Image
                src={item.product.mainImageUrl}
                alt={item.product.name}
                fluid
                rounded
              />
            </Col>
            <Col xs="auto" md={3}>
              <Link to={`/product/${item.product._id}`}>
                {item.product.name}
              </Link>
            </Col>
            <Col xs={3}>${item.product.price}</Col>

            <Col xs={2} md={3}>
              <CountInStock2
                qty={item.qty}
                product={item.product}
                updateQty={updateQty}
                updateProduct={updateProduct}
                countInStock={item.product.countInStock}
              />
            </Col>
            <Col xs={1} md={1}>
              <Button
                type="button"
                size="sm"
                variant="danger"
                onClick={() => removeProductFromCart(item.product._id)}
              >
                <i className="fas fa-trash"></i>
              </Button>
            </Col>
          </Row>
        </ListGroup.Item>
      ))}
    </ListGroup>
  );
};
