import { AxiosResponse } from "axios";
import Swal from "sweetalert2";

import { AppThunk } from "../../app/store";
import { initLogin, finishLogin, login } from "./authSlice";
import { getMsgError } from "../../app/helpers";
import { instance } from "../../app/axiosInstance";
import { setError } from "../ui/uiSlice";
import { IUser } from "../../schemas";
import { saveToLocalStorage } from "../../app/helpers";

export const startLogin =
  (email: string, password: string): AppThunk =>
  async (dispatch) => {
    dispatch(initLogin());

    try {
      const response: AxiosResponse = await instance.post("/api/users/login/", {
        username: email,
        password: password,
      });

      // console.log(response.data);

      const user: IUser = {
        _id: response.data._id,
        name: response.data.name,
        email: response.data.email,
        token: response.data.token,
        isAdmin: response.data.isAdmin,
      };
      saveToLocalStorage("user", user);
      dispatch(login(user));
      dispatch(finishLogin());
    } catch (err: any) {
      dispatch(finishLogin());
      const msgError = getMsgError(err);
      dispatch(setError(msgError));
      Swal.fire("Error", msgError, "error");
    }
  };

export const startUserRegister =
  (name: string, email: string, password: string): AppThunk =>
  async (dispatch) => {
    dispatch(initLogin());

    try {
      const response: AxiosResponse = await instance.post(
        "/api/users/register/",
        { email: email, password: password, name: name }
      );

      const user: IUser = {
        _id: response.data._id,
        name: response.data.name,
        email: response.data.email,
        token: response.data.token,
      };
      // Guardar a localStorage
      saveToLocalStorage("user", user);
      dispatch(login(user));
      dispatch(finishLogin());
    } catch (err: any) {
      dispatch(finishLogin());
      const msgError = getMsgError(err);
      dispatch(setError(msgError));
      Swal.fire("Error", msgError, "error");
    }
  };

export const updateUserProfile =
  (name: string, email: string, password: string): AppThunk =>
  async (dispatch) => {
    dispatch(initLogin());

    try {
      const response: AxiosResponse = await instance.put(
        "/api/users/profile/update/",
        { email: email, password: password, name: name }
      );

      const user: IUser = {
        _id: response.data._id,
        name: response.data.name,
        email: response.data.email,
        token: response.data.token,
      };
      // Guardar a localStorage
      saveToLocalStorage("user", user);
      dispatch(login(user));
      dispatch(finishLogin());
    } catch (err: any) {
      dispatch(finishLogin());
      const msgError = getMsgError(err);
      dispatch(setError(msgError));
      Swal.fire("Error", msgError, "error");
    }
  };
