import React from "react";
import { Pagination } from "react-bootstrap";

interface Props {
  pages: number;
  page: number;
  isAdmin?: boolean;
  search: boolean;
  handleClick: (page: number) => void;
}

const Paginate: React.FC<Props> = ({
  pages,
  page,
  isAdmin = false,
  search,
  handleClick,
}) => {
  let numPages = [];
  for (let i = 1; i <= Number(pages); i++) {
    numPages.push(Number(i));
  }

  return (
    <Pagination>
      {numPages.map((x) => (
        <Pagination.Item
          key={x}
          active={x === page}
          onClick={() => handleClick(x)}
        >
          {x}
        </Pagination.Item>
      ))}
    </Pagination>
  );
};

export default Paginate;

