import React, { VFC } from "react";
import { Link } from "react-router-dom";
import { Row, Col, Image, ListGroup, Button } from "react-bootstrap";
import { useAppDispatch } from "../../app/hooks";

import { IOrderInDetails } from "../../schemas";
import { Message } from "../../components/Message";
import { payOrder } from "./MyOrderAPI";

export const OrderDetails: VFC<IOrderInDetails> = (order) => {
  const dispatch = useAppDispatch();
  return (
    <ListGroup variant="flush">
      <ListGroup.Item>
        <h2>Order {order._id}</h2>
        <p>
          <strong>Shipping: </strong> <strong>Name:</strong> {order.user.name}{" "}
        </p>
        <p>
          {" "}
          <strong>Email:</strong>{" "}
          <a href={`mailto:${order.user.email}`}>{order.user.email}</a>{" "}
        </p>
        <p>
          {order.shippingAddress?.address} {order.shippingAddress?.city}{" "}
          {order.shippingAddress?.postalCode}, {order.shippingAddress?.country}
        </p>

        {order.isDelivered ? (
          <Message variant="success">Delivered on {order.deleveredAt}</Message>
        ) : (
          <Message variant="warning">Not delivered</Message>
        )}
      </ListGroup.Item>
      <ListGroup.Item>
        <h2>Payment Method</h2>
        <p>
          <strong>Method: </strong>
          {order.paymentMethod}
        </p>
        {order.isPaid ? (
          <Message variant="success">Paid on {order.paidAt}</Message>
        ) : (
          <>
            <Message variant="warning">Not paid</Message>
            <Button
              type="button"
              variant="primary"
              onClick={() => dispatch(payOrder(order._id))}
            >
              Pay Order
            </Button>
          </>
        )}
      </ListGroup.Item>
      <ListGroup.Item>
        <ListGroup variant="flush">
          {order.orderItems.map((item, idx) => (
            <ListGroup.Item key={item._id}>
              <Row>
                <Col md={1}>
                  <Image src={item.image} alt={item.name} fluid rounded />
                </Col>
                <Col>
                  <Link to={`/product/${item.product}`}>{item.name}</Link>
                </Col>
                <Col md={4}>
                  {item.qty} X ${item.price} = $
                  {(item.qty * item.price).toFixed(2)}
                </Col>
              </Row>
            </ListGroup.Item>
          ))}
        </ListGroup>
      </ListGroup.Item>
    </ListGroup>
  );
};
