import React, { FC, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { RouteComponentProps } from "react-router";
import { Form, Button, Row, Col, Container } from "react-bootstrap";

import { useAppDispatch, useAppSelector } from "../app/hooks";
import { useForm } from "../app/hooks/useForm";
import { Message } from "../components/Message";
import { isChecking, getUser } from "../features/Auth/authSlice";
import { isError, removeError } from "../features/ui/uiSlice";
import { startUserRegister } from "../features/Auth/authAPI";
import { LoadSpinner } from "../components/LoadSpinner";

export const RegisterScreen: FC<RouteComponentProps> = ({
  location,
  history,
}) => {
  const [message, setMessage] = useState<string | undefined>(undefined);

  const dispatch = useAppDispatch();
  const checking = useAppSelector(isChecking);
  const error = useAppSelector(isError);
  const user = useAppSelector(getUser);
  const redirect = location.search ? location.search.split("=")[1] : "/";

  interface formProps {
    name: string;
    email: string;
    password: string;
    confirmPassword: string;
  }

  const [formValues, actions] = useForm<formProps>({
    name: "",
    email: "",
    password: "",
    confirmPassword: "",
  });

  const {
    values: { name, email, password, confirmPassword },
  } = formValues;
  const { handleInputChange, reset } = actions;

  const submitHandler = (e: React.FormEvent) => {
    e.preventDefault();

    if (password !== confirmPassword) {
      setMessage("Passwords do not match");
    } else {
      dispatch(startUserRegister(name, email, password));
    }

    if (!!error && !!checking) {
      reset();
    }
  };

  useEffect(() => {
    return () => {
      dispatch(removeError());
    };
  }, [dispatch]);

  useEffect(() => {
    if (user) {
      history.push(redirect);
    }
  }, [history, user, redirect]);
  return (
    <Container>
      <Row className="justify-content-md-center">
        <Col xs={12} md={6}>
          <h1>Sign In</h1>
          {message && <Message variant="danger">{message}</Message>}
          {error && <Message variant="danger">{error}</Message>}
          <Form onSubmit={submitHandler}>
            <Form.Group controlId="name">
              <Form.Label>Name</Form.Label>
              <Form.Control
                required
                type="text"
                placeholder="Enter your name"
                name="name"
                value={name}
                onChange={handleInputChange}
              ></Form.Control>
            </Form.Group>
            <Form.Group controlId="email">
              <Form.Label>Email Address</Form.Label>
              <Form.Control
                required
                type="email"
                placeholder="Enter Email"
                name="email"
                value={email}
                onChange={handleInputChange}
              ></Form.Control>
            </Form.Group>
            <Form.Group controlId="password">
              <Form.Label>Password</Form.Label>
              <Form.Control
                required
                type="password"
                placeholder="Enter Password"
                name="password"
                value={password}
                onChange={handleInputChange}
              ></Form.Control>
            </Form.Group>
            <Form.Group controlId="confirmpassword">
              <Form.Label>confirm Password</Form.Label>
              <Form.Control
                required
                type="password"
                placeholder="Enter Password"
                name="confirmPassword"
                value={confirmPassword}
                onChange={handleInputChange}
              ></Form.Control>
            </Form.Group>
            <Button
              type="submit"
              variant="primary"
              className="mt-2"
              disabled={checking}
            >
              Register
            </Button>
            {checking ? <LoadSpinner /> : undefined}
          </Form>
          <Row className="py-3">
            <Col>
              <p>
                Have an Account?
                <Link to={redirect ? `/login?redirect=${redirect}` : "/login"}>
                  Sign In
                </Link>
              </p>
            </Col>
          </Row>
        </Col>
      </Row>
    </Container>
  );
};
