import React, { FC } from "react";
import { Form } from "react-bootstrap";
import { IProduct } from "../../schemas";

type Props = {
  qty: number;
  countInStock: number;
  product?: IProduct;
  updateQty: Function;
  updateProduct?: Function;
};

export const CountInStock2: FC<Props> = ({
  qty,
  countInStock,
  updateProduct,
  updateQty,
  product,
}) => {
  const optionsNumber: number[] = [];
  for (let i = 0; i < countInStock; i++) {
    optionsNumber.push(i + 1);
  }
  const onChangeHandler = (e: any) => {
    updateQty(e);
    if (product && updateProduct) {
      updateProduct(product);
    }
  };
  return (
    <Form.Control
      as="select"
      value={String(qty)}
      onChange={(e) => onChangeHandler(e as any)}
      className="mx-2"
    >
      {optionsNumber.map((x) => (
        <option key={x} value={x}>
          {x}
        </option>
      ))}
    </Form.Control>
  );
};
