import { AxiosResponse } from "axios";
import Swal from "sweetalert2";
import { instance } from "../../app/axiosInstance";
import { AppThunk } from "../../app/store";

import { getMsgError } from "../../app/helpers";
import {
  setLoading,
  setReviews,
  ProductReviewState,
  setSelectedProductReviews,
  setLoadingReview,
  setCreateReviewSuccess,
} from "./ReviewsSlice";
import {
  setSelectedProduct,
  setProducts,
  setProductsPages,
  setcurrentProductsPerPage,
} from "../products/productSlice";
import {
  IReviewCreate,
  IProductReview,
  IProductPages,
  IProduct,
} from "../../schemas";
import { updateProdPages, getProductsPerPage } from "../products/productAPI";

export const createProductReview =
  (productId: string, review: IReviewCreate): AppThunk =>
  async (dispatch, getState) => {
    const productsReviews = getState().reviews.reviews;
    const productPages = getState().products.productsPages;
    const currentProdsPerPage = getState().products.currentProductsPerPage;
    const pagination = getState().products.pagination;
    //const products = getState().products.products;

    if (!currentProdsPerPage) {
      return;
    }

    let productToUpdate: IProduct | undefined;
    productToUpdate = currentProdsPerPage.products.find(
      (prod) => Number(prod._id) === Number(productId)
    );

    if (!productToUpdate) {
      const msg = `Loading product in the background, please wait 2s and try again`;
      dispatch(setLoadingReview(false));
      dispatch(setCreateReviewSuccess(false));
      dispatch(getProductsPerPage(pagination.page));
      Swal.fire("Error", msg, "error");
      return;
    }

    dispatch(setLoadingReview(true));

    try {
      const response: AxiosResponse = await instance.post(
        `/api/products/${productId}/reviews/create/`,
        review
      );

      const data = response.data;
      const newProductReview: IProductReview = { ...data.review };

      let updatedProducReviews2: IProductReview[] = [];
      const updatedProductsReviews: ProductReviewState[] = productsReviews.map(
        (review: ProductReviewState) => {
          if (Number(review.productId) === Number(productId)) {
            updatedProducReviews2 = [
              ...review.productReviews,
              newProductReview,
            ];
            return {
              productId,
              productReviews: updatedProducReviews2,
            };
          }
          return review;
        }
      );

      const updatedProduct = {
        ...productToUpdate,
        rating: data.product_rating,
        numReviews: data.product_num_reviews,
      };

      const updatedProdsPerPage = currentProdsPerPage.products.map((prod) => {
        if (Number(prod._id) === Number(productId)) {
          return updatedProduct;
        }

        return prod;
      });

      const updatedProdPage: IProductPages = {
        page: currentProdsPerPage.page,
        products: updatedProdsPerPage,
      };
      const updatedProdPages = updateProdPages(productPages, updatedProdPage);

      dispatch(setReviews(updatedProductsReviews));
      dispatch(setSelectedProduct(updatedProduct));

      dispatch(setSelectedProductReviews(updatedProducReviews2));
      dispatch(setProductsPages(updatedProdPages));
      dispatch(setcurrentProductsPerPage(updatedProdPage));
      dispatch(setLoadingReview(false));
      dispatch(setCreateReviewSuccess(true));
      Swal.fire({
        position: "top-end",
        icon: "success",
        title: "Your review has been submitted!",
        showConfirmButton: false,
        timer: 2000,
      });
    } catch (err: any) {
      dispatch(setLoadingReview(false));
      dispatch(setCreateReviewSuccess(false));
      const msgError = getMsgError(err);
      Swal.fire("Error", msgError, "error");
    }
  };

export const fetchProductReviews =
  (productId: string): AppThunk =>
  async (dispatch, getState) => {
    const allProductReviews = getState().reviews.reviews;
    dispatch(setLoading(true));

    try {
      const response: AxiosResponse = await instance.get(
        `/api/products/${productId}/reviews/`
      );

      const productReviews: IProductReview[] = response.data;

      const updatedAllProdReviews = [
        ...allProductReviews,
        { productId, productReviews },
      ];

      dispatch(setReviews(updatedAllProdReviews));
      dispatch(setSelectedProductReviews(productReviews));

      dispatch(setLoading(false));
    } catch (err: any) {
      dispatch(setLoading(false));
      const msgError = getMsgError(err);
      Swal.fire("Error", msgError, "error");
    }
  };

export const getProductReviews =
  (productId: string): AppThunk =>
  async (dispatch, getState) => {
    const allProductReviews = getState().reviews.reviews;

    const selectedProductReviews = allProductReviews.find(
      (review) => Number(review.productId) === Number(productId)
    );

    if (selectedProductReviews) {
      dispatch(
        setSelectedProductReviews(selectedProductReviews.productReviews)
      );
      return;
    }

    dispatch(fetchProductReviews(productId));
  };
