import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import Swal from "sweetalert2";
import { AppThunk } from "../../app/store";
import { RootState } from "../../app/store";
import { IUserBase } from "../../schemas";

interface IUsersState {
  loading: boolean;
  success: boolean;
  error: string | null;
  users: IUserBase[];
  selectedUser: IUserBase | null;
}

const initialState: IUsersState = {
  loading: false,
  success: false,
  error: null,
  users: [],
  selectedUser: null,
};

export const setSelectedUserById =
  (id: string): AppThunk =>
  (dispatch, getState) => {
    const users = getState().users.users;

    const userLocal = users.find((u) => Number(u._id) === Number(id));

    if (!userLocal) {
      Swal.fire(
        "Error",
        `USUARIO CON ID: ${id} NO ENCONTRADO EN MEMORIA`,
        "error"
      );
      dispatch(removeSelectedUser());
      return;
    }

    dispatch(setSelectedUser(userLocal));
  };

export const usersSlice = createSlice({
  name: "users",
  initialState,
  reducers: {
    startLoadingUsers: (state) => {
      state.loading = true;
    },
    finishLoadingUsers: (state) => {
      state.loading = false;
    },
    setUsersError: (state, action: PayloadAction<string>) => {
      state.error = action.payload;
    },
    removeUsersError: (state) => {
      state.error = null;
    },
    resetUsers: (state) => {
      state.users = [];
    },
    setUsers: (state, action: PayloadAction<IUserBase[]>) => {
      state.users = action.payload;
    },
    setSuccess: (state, action: PayloadAction<boolean>) => {
      state.success = action.payload;
    },
    setSelectedUser: (state, action: PayloadAction<IUserBase>) => {
      state.selectedUser = action.payload;
    },
    removeSelectedUser: (state) => {
      state.selectedUser = null;
    },
  },
});

export const {
  startLoadingUsers,
  finishLoadingUsers,
  setUsersError,
  removeUsersError,
  resetUsers,
  setUsers,
  setSuccess,
  setSelectedUser,
  removeSelectedUser,
} = usersSlice.actions;

export const getUsers = (state: RootState) => state.users.users;
export const isLoadingUsers = (state: RootState) => state.users.loading;
export const getErrorUsers = (state: RootState) => state.users.error;
export const getUsersState = (state: RootState) => state.users;
export const getUsersSuccess = (state: RootState) => state.users.success;
export const getSelectedUser = (state: RootState) => state.users.selectedUser;

export default usersSlice.reducer;
