import React, { useEffect } from "react";

import { useAppDispatch } from "./app/hooks";

import { loadFromLocalStorage } from "./app/helpers";

import { AppRouter } from "./routers/AppRouter";
import {
  updateItems,
  updateShippingAddress,
  updatePaymentMethod,
} from "./features/Cart/cartSlice";
import { login } from "./features/Auth/authSlice";

const App = () => {
  const dispatch = useAppDispatch();

  useEffect(() => {
    const cartItemsFromLS = loadFromLocalStorage("cartItems");
    const userFromLS = loadFromLocalStorage("user");
    const shippingAddressFromLS = loadFromLocalStorage("shippingAddress");
    const paymentMethodFromLS = loadFromLocalStorage("paymentMethod");
    if (cartItemsFromLS) {
      dispatch(updateItems(cartItemsFromLS));
    }
    if (userFromLS) {
      dispatch(login(userFromLS));
    }
    if (shippingAddressFromLS) {
      dispatch(updateShippingAddress(shippingAddressFromLS));
    }
    if (paymentMethodFromLS) {
      dispatch(updatePaymentMethod(paymentMethodFromLS));
    }
  }, [dispatch]);

  return <AppRouter />;
};

export default App;
