import React, { useEffect, FC } from "react";
import { LinkContainer } from "react-router-bootstrap";
import { Table, Button, Row, Col } from "react-bootstrap";
import { RouteComponentProps } from "react-router";

import { useAppDispatch, useAppSelector } from "../app/hooks";
import { Loading } from "../components/loading/loading";
import { Message } from "../components/Message";
import { getAuthState } from "../features/Auth/authSlice";

import {
  getProductsState,
  setSelectedProductById,
} from "../features/products/productSlice";

import {
  deleteProduct,
  getProductsPerPage,
} from "../features/products/productAPI";
import Paginate from "../components/Paginate";

const ProductListScreen: FC<RouteComponentProps> = ({ history }) => {
  const dispatch = useAppDispatch();

  const productsState = useAppSelector(getProductsState);
  const authState = useAppSelector(getAuthState);

  const {
    currentProductsPerPage,
    loading,
    hasErrors: error,
    pagination,
  } = productsState;

  const { isLoggedIn } = authState;
  const products = currentProductsPerPage
    ? currentProductsPerPage.products
    : [];

  useEffect(() => {
    if (products.length < 1 && !loading && !error && isLoggedIn) {
      dispatch(getProductsPerPage(pagination.page));
    }
  }, [dispatch, products, loading, error, isLoggedIn, pagination.page]);

  useEffect(() => {
    if (!isLoggedIn) {
      history.push("/login");
    }
  }, [isLoggedIn, history]);

  const deleteProductHandler = (id: string) => {
    if (window.confirm("Are you sure you want to delete this product?")) {
      dispatch(deleteProduct(id));
    }
  };

  const productToEditHandler = (productId: string) => {
    dispatch(setSelectedProductById(productId));
  };

  const createProductHandler = () => {
    // create product
    history.push("/admin/product/create");
  };

  const handlePaginationClick = (page: number) => {
    dispatch(getProductsPerPage(page));
  };

  return (
    <div>
      <Row className="align-items-center">
        <Col>
          <h1>Products</h1>
        </Col>
        <Col className="text-right">
          <Button className="my-3" onClick={createProductHandler}>
            <i className="fas fa-plus"></i> Create Product
          </Button>
        </Col>
      </Row>
      {loading && <Loading />}
      {error && <Message variant="danger">{error}</Message>}
      {!isLoggedIn && (
        <h3>
          Inicia sesion <LinkContainer to={"/login"}>aqu</LinkContainer> para
          ver la lista de productos
        </h3>
      )}
      {isLoggedIn && products.length > 0 ? (
        <>
          <Table striped bordered hover responsive className="table-sm">
            <thead>
              <tr>
                <th>ID</th>
                <th>NAME</th>
                <th>PRICE</th>
                <th>CATEGORY</th>
                <th>BRAND</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {products.map((product) => (
                <tr key={product._id}>
                  <td>{product._id}</td>
                  <td>{product.name}</td>
                  <td>$ {product.price}</td>
                  <td>{product.category}</td>
                  <td>{product.brand}</td>
                  <td>
                    <LinkContainer to={`/admin/product/${product._id}/edit`}>
                      <Button
                        variant="light"
                        className="btn-sm"
                        onClick={() => productToEditHandler(product._id)}
                      >
                        <i className="fas fa-edit"></i>
                      </Button>
                    </LinkContainer>
                    <Button
                      variant="danger"
                      className="btn-sm"
                      onClick={() => deleteProductHandler(product._id)}
                    >
                      <i className="fas fa-trash"></i>
                    </Button>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
          <Paginate
            page={pagination.page}
            pages={pagination.pages}
            search={false}
            handleClick={handlePaginationClick}
          />
        </>
      ) : (
        <h3>Aun no has creado ningun producto</h3>
      )}
    </div>
  );
};

export default ProductListScreen;
