import React, { FC } from "react";
import { RouteComponentProps } from "react-router";
import { Form, Button, Row, Col, Container } from "react-bootstrap";

import { useAppDispatch, useAppSelector } from "../app/hooks";
import { useForm } from "../app/hooks/useForm";
import {
  getShippingAddress,
  updateShippingAddressLS,
} from "../features/Cart/cartSlice";
import { IShippingAddress } from "../schemas";
import { CheckoutSteps } from "../features/CheckoutSteps";

export const ShippingScreen: FC<RouteComponentProps> = ({ history }) => {
  const shippingAddress = useAppSelector(getShippingAddress);
  const dispatch = useAppDispatch();

  const [formValues, actions] = useForm<IShippingAddress>({
    address: shippingAddress?.address || "",
    city: shippingAddress?.city || "",
    postalCode: shippingAddress?.postalCode || "",
    country: shippingAddress?.country || "",
  });

  const {
    values: { address, city, postalCode, country },
  } = formValues;
  const { handleInputChange } = actions;

  const submitHandler = (e: React.FormEvent) => {
    e.preventDefault();

    dispatch(
      updateShippingAddressLS({
        address,
        city,
        postalCode,
        country,
      })
    );

    history.push("/payment");
  };
  return (
    <Container>
      <Row className="justify-content-md-center">
        <Col xs={12} md={6}>
          <CheckoutSteps step1={true} step2={true} />
          <h1>Shipping</h1>
          <Form onSubmit={submitHandler}>
            <Form.Group controlId="Address">
              <Form.Control
                required
                type="text"
                placeholder="Enter your address"
                name="address"
                value={address}
                onChange={handleInputChange}
              ></Form.Control>
            </Form.Group>

            <Form.Group controlId="City">
              <Form.Control
                required
                type="text"
                placeholder="Enter your city"
                name="city"
                value={city}
                onChange={handleInputChange}
              ></Form.Control>
            </Form.Group>

            <Form.Group controlId="PostalCode">
              <Form.Control
                required
                type="number"
                placeholder="Enter your postal Code"
                name="postalCode"
                value={postalCode}
                onChange={handleInputChange}
              ></Form.Control>
            </Form.Group>

            <Form.Group controlId="Country">
              <Form.Control
                required
                type="text"
                placeholder="Enter your country"
                name="country"
                value={country}
                onChange={handleInputChange}
              ></Form.Control>
            </Form.Group>

            <Button type="submit" variant="primary" className="mt-3">
              Continue
            </Button>
          </Form>
        </Col>
      </Row>
    </Container>
  );
};
