import { AxiosResponse } from "axios";
import Swal from "sweetalert2";

import { instance } from "../../app/axiosInstance";
import { AppThunk } from "../../app/store";
import {
  IOrderInDetails,
  IOrderOut,
  IOrderItem,
  IProductPages,
} from "../../schemas";
import {
  setCurrentOrder,
  setSuccess,
  startLoadingOrder,
  finishLoadingOrder,
  setPrices,
  setOrders,
  setOrderError,
} from "./MyOrdersSlice";

import {
  setProductsPages,
  setcurrentProductsPerPage,
} from "../products/productSlice";

import { startLoading, stopLoading, setError } from "../ui/uiSlice";
import { resetCartLS } from "../Cart/cartSlice";
import { getMsgError } from "../../app/helpers";
import { setOrders as setAllOrders } from "../Orders/OrdersSlice";

const updateProducQtys = (
  orderItems: IOrderItem[],
  prodPages: IProductPages[]
) => {
  console.log("prodPages before qty update: ", prodPages);
  console.log("orderedItems: ", orderItems);
  const updatedProdPages = prodPages.map((prodPage) => {
    const prodPageUpdated: IProductPages = {
      page: prodPage.page,
      products: [],
    };

    prodPage.products.forEach((product) => {
      const filter = orderItems.find(
        (ordItem) => Number(ordItem.product) === Number(product._id)
      );
      if (filter) {
        const updatedProduct = {
          ...product,
          countInStock: product.countInStock - filter.qty,
        };
        prodPageUpdated.products.push(updatedProduct);
      } else {
        prodPageUpdated.products.push(product);
      }
    });

    return prodPageUpdated;
  });
  console.log("prodPages after qty update: ", updatedProdPages);
  return updatedProdPages;
};

export const createOrder =
  (order: IOrderOut): AppThunk =>
  async (dispatch, getState) => {
    const prodPages = getState().products.productsPages;
    dispatch(startLoading());
    try {
      const response: AxiosResponse = await instance.post(
        "/api/orders/add/",
        order
      );

      const newOrder: IOrderInDetails = {
        _id: response.data._id,
        orderItems: response.data.orderItems,
        paymentMethod: response.data.paymentMethod,
        shippingAddress: response.data.shippingAddress,
        shippingPrice: response.data.shippingPrice,
        subTotal: response.data.subTotal,
        taxPrice: response.data.taxPrice,
        totalPrice: response.data.totalPrice,
        createdAt: response.data.createdAt,
        deleveredAt: response.data.deleveredAt,
        isDelivered: response.data.isDelivered,
        isPaid: response.data.isPaid,
        paidAt: response.data.paidAt,
        user: response.data.user,
      };

      const updatedProdPages = updateProducQtys(newOrder.orderItems, prodPages);

      dispatch(setCurrentOrder(newOrder));
      dispatch(setAllOrders([newOrder]));
      dispatch(setProductsPages(updatedProdPages));
      dispatch(setcurrentProductsPerPage(undefined));
      dispatch(setSuccess(true));
      dispatch(resetCartLS());
      dispatch(stopLoading());
    } catch (err: any) {
      dispatch(stopLoading());
      dispatch(setSuccess(false));
      const msgError = getMsgError(err);
      dispatch(setError(msgError));
      Swal.fire("Error", msgError, "error");
    }
  };

