import { AxiosResponse } from "axios";
import { AppThunk } from "../../app/store";
import Swal from "sweetalert2";
import {
  IProduct,
  ICreateProduct,
  IImage,
  IProductSearchResults,
} from "../../schemas";
import { instance } from "../../app/axiosInstance";
import { IProductPages } from "../../schemas";
import {
  getMsgError,
  productImageUpload,
  deleteImage,
} from "../../app/helpers";
import {
  initLoading,
  finishLoading,
  setProductError,
  setReqSuccess,
  setPagination,
  setcurrentProductsPerPage,
  setProductsPages,
  setSearchPagination,
  setCurrentSearchResults,
  setProductSearchresults,
  setIsSearchResults,
  setTopProducts,
  setLastPageWithDelProd,
  setSelectedProduct,
} from "./productSlice";

export const fetchTopProducts = (): AppThunk => async (dispatch) => {
  dispatch(initLoading());
  try {
    const response: AxiosResponse = await instance.get<IProduct[]>(
      "/api/products/top"
    );

    const products: IProduct[] = response.data;

    dispatch(setTopProducts(products));
    dispatch(finishLoading());
  } catch (err: any) {
    dispatch(finishLoading());
    dispatch(setReqSuccess(false));
    const msgError = getMsgError(err);
    Swal.fire("Error", msgError, "error");
  }
};

export const updateProdPages = (
  prodPages: IProductPages[],
  prodPage: IProductPages
) => {
  let updatedProdPages: IProductPages[] = [];

  const prodPageToPudate = prodPages.find(
    (prod) => Number(prod.page) === Number(prodPage.page)
  );

  if (prodPageToPudate) {
    updatedProdPages = prodPages.map((prod) => {
      if (Number(prod.page) === Number(prodPage.page)) {
        return prodPage;
      }

      return prod;
    });
  } else {
    updatedProdPages = [...prodPages, prodPage];
  }

  return updatedProdPages;
};

export const fetchProducts =
  (kw = ""): AppThunk =>
  async (dispatch, getState) => {
    const productPages = getState().products.productsPages;

    dispatch(initLoading());

    try {
      const response: AxiosResponse = await instance.get<IProduct[]>(
        `/api/products${kw}`
      );

      const products: IProduct[] = response.data.products;
      const page = Number(response.data.page);
      const pages = Number(response.data.pages);
      const pageSize = Number(response.data.pageSize);
      const updatedProductPages = updateProdPages(productPages, {
        page,
        products,
      });

      dispatch(setPagination({ page, pages, pageSize }));

      dispatch(setProductsPages(updatedProductPages));
      dispatch(setcurrentProductsPerPage({ page, products }));
      dispatch(finishLoading());
    } catch (err: any) {
      dispatch(finishLoading());
      dispatch(setReqSuccess(false));
      const msgError = getMsgError(err);
      Swal.fire("Error", msgError, "error");
    }
  };

export const fetchProduct =
  (productId: string): AppThunk =>
  async (dispatch) => {
    dispatch(initLoading());

    try {
      const response: AxiosResponse = await instance.get<IProduct>(
        `/api/products/${productId}/`
      );

      const product: IProduct = response.data;

      dispatch(setSelectedProduct(product));
      dispatch(finishLoading());
    } catch (err: any) {
      dispatch(finishLoading());
      dispatch(setReqSuccess(false));
      const msgError = getMsgError(err);
      Swal.fire("Error", msgError, "error");
    }
  };

export const getProductsPerPage =
  (page: number): AppThunk =>
  async (dispatch, getState) => {
    const productPages = getState().products.productsPages;
    const pagination = getState().products.pagination;
    const lastPageWithDelProds = getState().products.lastPageWithDeletedProd;

    const keyword = `?page=${page}`;

    if (lastPageWithDelProds > 0 && page >= lastPageWithDelProds) {
      dispatch(fetchProducts(keyword));
      if (page === pagination.pages) {
        dispatch(setLastPageWithDelProd(0));
      }
      return;
    }

    const productsPerPage = productPages.find((p) => p.page === page);

    if (productsPerPage) {
      dispatch(
        setPagination({
          page,
          pages: pagination.pages,
          pageSize: pagination.pageSize,
        })
      );
      dispatch(setcurrentProductsPerPage(productsPerPage));
      return;
    }

    dispatch(fetchProducts(keyword));
  };

export const fetchSearchProducts =
  (kw = ""): AppThunk =>
  async (dispatch, getState) => {
    const searchResults = getState().products.searchResults;
    dispatch(initLoading());

    try {
      const response: AxiosResponse = await instance.get<IProduct[]>(
        `/api/products${kw}`
      );

      const products: IProduct[] = response.data.products;
      const page = Number(response.data.page);
      const pages = Number(response.data.pages);
      const pageSize = Number(response.data.pageSize);
      const newSearchResult: IProductSearchResults = {
        keyword: kw,
        productPages: [{ page, products }],
      };
      const updatedSearchResults = [...searchResults, newSearchResult];

      dispatch(setIsSearchResults(true));
      dispatch(setSearchPagination({ page, pages, pageSize }));

      dispatch(setProductSearchresults(updatedSearchResults));
      dispatch(setCurrentSearchResults({ page, products }));
      dispatch(finishLoading());
    } catch (err: any) {
      dispatch(finishLoading());
      dispatch(setReqSuccess(false));
      dispatch(setIsSearchResults(false));
      const msgError = getMsgError(err);
      Swal.fire("Error", msgError, "error");
    }
  };

export const getProductsSearch =
  (page: number): AppThunk =>
  async (dispatch, getState) => {
    const searchResults = getState().products.searchResults;
    const pagination = getState().products.searchPagination;
    const kwSearch = getState().products.keyword;

    const keyword = `?keyword=${kwSearch}&page=${page}`;
    const kwInMemery = searchResults.find((sr) => sr.keyword === keyword);

    if (kwInMemery) {
      const productSearchPage = kwInMemery.productPages.find(
        (p) => p.page === page
      );
      if (productSearchPage) {
        dispatch(setIsSearchResults(true));
        dispatch(
          setSearchPagination({
            page,
            pages: pagination.pages,
            pageSize: pagination.pageSize,
          })
        );
        dispatch(setCurrentSearchResults(productSearchPage));
        return;
      }
    }

    dispatch(fetchSearchProducts(keyword));
  };

export const getProductById = async (id: number) => {
  try {
    const response: AxiosResponse = await instance.get<IProduct>(
      `/api/products/${id}`
    );

    return response.data;
  } catch (err: any) {
    const msgError = getMsgError(err);
    Swal.fire("Error", msgError, "error");
  }
};

export const createProduct =
  (product: ICreateProduct, images?: FileList): AppThunk =>
  async (dispatch, getState) => {
    const productPages = getState().products.productsPages;
    const pagination = getState().products.pagination;

    dispatch(initLoading());
    try {
      const res: AxiosResponse = await instance.post<IProduct>(
        `/api/products/create/`,
        product
      );

      const newProduct: IProduct = res.data;

      if (images) {
        const productImages = await productImageUpload(images, newProduct._id);

        if (!productImages) {
          Swal.fire(
            "Error",
            "El producto fue creado, pero hubo error al cargar las imagenes!",
            "error"
          );
        }
        newProduct.images = productImages.images;
        newProduct.mainImageUrl = productImages.mainImageUrl;
      }

      const lastProductPage = productPages.find(
        (prodPage) => Number(prodPage.page) === Number(pagination.pages)
      );

      let productPage: IProductPages;
      let updatedProductPages: IProductPages[] = [];

      if (
        lastProductPage &&
        lastProductPage?.products.length < pagination.pageSize
      ) {
        productPage = {
          page: lastProductPage.page,
          products: [...lastProductPage.products, newProduct],
        };
        updatedProductPages = productPages.map((prodPage) => {
          if (Number(prodPage.page) === Number(lastProductPage.page)) {
            return productPage;
          }

          return prodPage;
        });
        dispatch(setcurrentProductsPerPage(productPage));
      } else {
        const newPage = pagination.pages + 1;
        const newProductPage: IProductPages = {
          page: newPage,
          products: [newProduct],
        };

        updatedProductPages = [...productPages, newProductPage];
        dispatch(
          setPagination({
            page: newPage,
            pages: newPage,
            pageSize: pagination.pageSize,
          })
        );
        dispatch(setcurrentProductsPerPage(newProductPage));
      }

      //dispatch(setProducts(updatedProducts));
      dispatch(setProductsPages(updatedProductPages));

      dispatch(setReqSuccess(true));
      dispatch(finishLoading());
    } catch (err: any) {
      dispatch(finishLoading());
      dispatch(setReqSuccess(false));
      const msgError = getMsgError(err);
      dispatch(setProductError());
      Swal.fire("Error", msgError, "error");
    }
  };

export const deleteProduct =
  (id: string): AppThunk =>
  async (dispatch, getState) => {
    const currentProdsPerPage = getState().products.currentProductsPerPage;

    if (!currentProdsPerPage) {
      return;
    }

    const productLocal = currentProdsPerPage.products.find(
      (p) => Number(p._id) === Number(id)
    );
    if (!productLocal) {
      Swal.fire(
        "Error",
        `Producto CON ID: ${id} NO ENCONTRADO EN MEMORIA`,
        "error"
      );
      return;
    }

    dispatch(initLoading());
    try {
      const res: AxiosResponse = await instance.delete(
        `/api/products/delete/${id}/`
      );

      dispatch(setLastPageWithDelProd(currentProdsPerPage.page));
      dispatch(fetchTopProducts());

      dispatch(getProductsPerPage(currentProdsPerPage.page));
      dispatch(setReqSuccess(true));
      dispatch(finishLoading());
    } catch (err: any) {
      dispatch(finishLoading());
      dispatch(setReqSuccess(false));
      const msgError = getMsgError(err);
      dispatch(setProductError());
      Swal.fire("Error", msgError, "error");
    }
  };

export const updateProduct =
  (
    product: IProduct,
    imagesToUpload?: FileList,
    imagesToDelete?: IImage[]
  ): AppThunk =>
  async (dispatch, getState) => {
    const productPages = getState().products.productsPages;
    const currentProdsPerPage = getState().products.currentProductsPerPage;

    if (!currentProdsPerPage) {
      return;
    }

    const productLocal = currentProdsPerPage.products.find(
      (p) => Number(p._id) === Number(product._id)
    );

    if (!productLocal) {
      Swal.fire(
        "Error",
        `PRODUCTO CON ID: ${product._id} NO ENCONTRADO EN MEMORIA`,
        "error"
      );
      return;
    }

    dispatch(initLoading());
    try {
      const res: AxiosResponse = await instance.put(
        `/api/products/update/${product._id}/`,
        product
      );

      const updatedProduct: IProduct = res.data;

      if (imagesToUpload && imagesToUpload.length > 0) {
        const uploadedProductImgs = await productImageUpload(
          imagesToUpload,
          product._id
        );
        const updatedProductImages = product.images
          ? [...product.images, ...uploadedProductImgs.images]
          : [...uploadedProductImgs];
        updatedProduct.images = updatedProductImages;
      }

      if (imagesToDelete && imagesToDelete.length > 0) {
        await deleteImage(imagesToDelete);
        imagesToDelete.forEach((imgToDelete) => {
          updatedProduct.images = updatedProduct.images?.filter(
            (img) => Number(img._id) !== Number(imgToDelete._id)
          );
        });
      }

      const updatedProdsPerPage = currentProdsPerPage.products.map((prod) => {
        if (Number(prod._id) === Number(product._id)) {
          return updatedProduct;
        }

        return prod;
      });

      const updatedProdPage: IProductPages = {
        page: currentProdsPerPage.page,
        products: updatedProdsPerPage,
      };

      const updatedProdPages = updateProdPages(productPages, updatedProdPage);

      dispatch(setProductsPages(updatedProdPages));
      dispatch(setcurrentProductsPerPage(updatedProdPage));
      dispatch(fetchTopProducts());
      dispatch(setReqSuccess(true));
      dispatch(finishLoading());
    } catch (err: any) {
      dispatch(finishLoading());
      dispatch(setReqSuccess(false));
      const msgError = getMsgError(err);
      dispatch(setProductError());
      Swal.fire("Error", msgError, "error");
    }
  };
