import React, { useEffect, FC } from "react";
import { LinkContainer } from "react-router-bootstrap";
import { Table, Button, Row, Col } from "react-bootstrap";
import { RouteComponentProps } from "react-router";

import { useAppDispatch, useAppSelector } from "../../app/hooks";
import { Loading } from "../../components/loading/loading";
import { getOrdersState } from "../../features/Orders/OrdersSlice";
import { fetchAllOrders, getAllOrders } from "../../features/Orders/OrdersAPI";
import { setCurrentOrder } from "../../features/MyOrders/MyOrdersSlice";

export const OrderListScreen: FC<RouteComponentProps> = ({ history }) => {
  const dispatch = useAppDispatch();
  const ordersState = useAppSelector(getOrdersState);

  const { isLoading, orders } = ordersState;

  useEffect(() => {
    dispatch(getAllOrders());
  }, [dispatch]);

  return (
    <div>
      <Row className="align-items-center">
        <Col>
          <h1>Orders</h1>
        </Col>
        <Col className="text-right">
          <Button className="my-3" onClick={() => dispatch(fetchAllOrders())}>
            <i className="fas fa-plus"></i> Refresh Orders
          </Button>
        </Col>
      </Row>
      {isLoading && <Loading />}
      {orders.length > 0 ? (
        <Table striped bordered hover responsive className="table-sm">
          <thead>
            <tr>
              <th>ID</th>
              <th>USER</th>
              <th>DATE</th>
              <th>TOTAL</th>
              <th>PAID</th>
              <th>DELIVERED</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {orders.map((order) => (
              <tr key={order._id}>
                <td>{order._id}</td>
                <td>{order.user.name}</td>
                <td>{order.createdAt && order.createdAt.substring(0, 10)}</td>
                <td>{order.totalPrice}</td>
                <td>
                  {order.isPaid ? (
                    <span>{order.paidAt.substring(0, 10)}</span>
                  ) : (
                    <i className="fas fa-ban" style={{ color: "red" }}></i>
                  )}
                </td>
                <td>
                  {order.isDelivered ? (
                    <span>
                      {order.deleveredAt && order.deleveredAt.substring(0, 10)}
                    </span>
                  ) : (
                    <i className="fas fa-ban" style={{ color: "red" }}></i>
                  )}
                </td>

                <td>
                  <LinkContainer to={`/order/${order._id}`}>
                    <Button
                      variant="light"
                      className="btn-sm"
                      onClick={() => dispatch(setCurrentOrder(order))}
                    >
                      Details
                    </Button>
                  </LinkContainer>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
      ) : undefined}
    </div>
  );
};
