import React, { FC, useState } from "react";
import { RouteComponentProps } from "react-router";
import { Form, Button, Row, Col, Container } from "react-bootstrap";

import { useAppDispatch, useAppSelector } from "../app/hooks";
import {
  getShippingAddress,
  savePaymentMethodLS,
} from "../features/Cart/cartSlice";
import { getTotalCartPrice } from "../features/MyOrders/MyOrderAPI";
import { CheckoutSteps } from "../features/CheckoutSteps";

export const PaymentScreen: FC<RouteComponentProps> = ({ history }) => {
  const dispatch = useAppDispatch();
  const shippingAddress = useAppSelector(getShippingAddress);

  const [paymentMethod, setPaymentMethod] = useState("PayPal");

  if (!shippingAddress?.address) {
    history.push("/shipping");
  }

  const submitHandler = (e: React.FormEvent) => {
    e.preventDefault();
    dispatch(savePaymentMethodLS(paymentMethod));
    dispatch(getTotalCartPrice());
    history.push("/placeorder");
  };

  return (
    <Container>
      <Row className="justify-content-md-center">
        <Col xs={12} md={6}>
          <CheckoutSteps step1={true} step2={true} step3={true} />
          <Form onSubmit={submitHandler}>
            <Form.Group controlId="Address">
              <Form.Label as="legend">Select Method</Form.Label>
              <Col>
                <Form.Check
                  type="radio"
                  label="PayPal or Credit Card"
                  id="paypal"
                  name="paymentMethod"
                  checked
                  onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                    setPaymentMethod(e.target.value)
                  }
                ></Form.Check>
              </Col>
            </Form.Group>

            <Button type="submit" variant="primary" className="mt-4">
              continue
            </Button>
          </Form>
        </Col>
      </Row>
    </Container>
  );
};
