import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { AppThunk } from "../../app/store";
import { RootState } from "../../app/store";
import {
  IProduct,
  IImage,
  IPagination,
  IProductPages,
  IProductSearchResults,
} from "../../schemas";

import { fetchProduct } from "./productAPI";

import Swal from "sweetalert2";

interface productsState {
  products: IProduct[];
  topProducts: IProduct[];
  productsPages: IProductPages[];
  currentProductsPerPage: IProductPages | undefined;
  lastPageWithDeletedProd: number;
  loading: boolean;
  hasErrors: boolean;
  reqSuccess: boolean;
  selectedProduct: IProduct | null;
  pagination: IPagination;
  keyword: string;
  isSearchResults: boolean;
  searchResults: IProductSearchResults[];
  currentSearchResults: IProductPages | undefined;
  searchPagination: IPagination;
}

const initialState: productsState = {
  products: [],
  topProducts: [],
  productsPages: [],
  currentProductsPerPage: { page: 1, products: [] },
  lastPageWithDeletedProd: 0,
  loading: false,
  hasErrors: false,
  reqSuccess: false,
  selectedProduct: null,
  pagination: { page: 1, pages: 1, pageSize: 4 },
  keyword: "",
  isSearchResults: false,
  searchResults: [],
  currentSearchResults: undefined,
  searchPagination: { page: 1, pages: 1, pageSize: 4 },
};

export const setSelectedProductById =
  (id: string): AppThunk =>
  (dispatch, getState) => {
    const currentProdsPerPage = getState().products.currentProductsPerPage;

    const productLocal = currentProdsPerPage?.products.find(
      (p) => Number(p._id) === Number(id)
    );

    if (!productLocal) {
      // Swal.fire(
      //   "Error",
      //   `PRODUCTO CON ID: ${id} NO ENCONTRADO EN MEMORIA`,
      //   "error"
      // );
      //dispatch(removeSelectedProduct());
      dispatch(fetchProduct(id));
      return;
    }

    dispatch(setSelectedProduct(productLocal));
  };

export const updateProducts =
  (productId: string, productImages: IImage[]): AppThunk =>
  (dispatch, getState) => {
    const productPages = getState().products.productsPages;
    const currentProdsPerPage = getState().products.currentProductsPerPage;

    const product = currentProdsPerPage?.products.find(
      (prod) => Number(prod._id) === Number(productId)
    );

    if (!product) {
      Swal.fire(
        "Error",
        `PRODUCTO CON ID: ${productId} NO ENCONTRADO EN MEMORIA`,
        "error"
      );
      return;
    }
    let updatedProduct = { ...product };

    updatedProduct.images = productImages.map((img) => {
      if (img.isMain) {
        updatedProduct.mainImageUrl = img.url;
      }
      return {
        _id: img._id,
        publicId: img.publicId,
        url: img.url,
        isMain: img.isMain,
      };
    });

    const updatedCurrentProdPage = currentProdsPerPage?.products.map(
      (product) => {
        if (Number(product._id) === Number(productId)) {
          return updatedProduct;
        }

        return product;
      }
    );

    if (updatedCurrentProdPage) {
      const updateduCurrentProdsPerPage: IProductPages = {
        page: currentProdsPerPage?.page || -1,
        products: updatedCurrentProdPage,
      };

      const updatedProdPages = productPages.map((prodPage) => {
        if (Number(prodPage.page) === Number(updatedCurrentProdPage)) {
          return updateduCurrentProdsPerPage;
        }
        return prodPage;
      });

      dispatch(setProductsPages(updatedProdPages));
      dispatch(setcurrentProductsPerPage(updateduCurrentProdsPerPage));
    }
  };

export const productSlice = createSlice({
  name: "products",
  initialState,
  reducers: {
    initLoading: (state) => {
      state.loading = true;
    },
    finishLoading: (state) => {
      state.loading = false;
    },
    setReqSuccess: (state, action: PayloadAction<boolean>) => {
      state.reqSuccess = action.payload;
    },
    setLastPageWithDelProd: (state, action: PayloadAction<number>) => {
      state.lastPageWithDeletedProd = action.payload;
    },
    setIsSearchResults: (state, action: PayloadAction<boolean>) => {
      state.isSearchResults = action.payload;
    },
    setProducts: (state, action: PayloadAction<IProduct[]>) => {
      state.products = action.payload;
    },
    setTopProducts: (state, action: PayloadAction<IProduct[]>) => {
      state.topProducts = action.payload;
    },
    setProductsPages: (state, action: PayloadAction<IProductPages[]>) => {
      state.productsPages = action.payload;
    },
    setcurrentProductsPerPage: (
      state,
      action: PayloadAction<IProductPages | undefined>
    ) => {
      state.currentProductsPerPage = action.payload;
    },
    setProductSearchresults: (
      state,
      action: PayloadAction<IProductSearchResults[]>
    ) => {
      state.searchResults = action.payload;
    },
    setCurrentSearchResults: (
      state,
      action: PayloadAction<IProductPages | undefined>
    ) => {
      state.currentSearchResults = action.payload;
    },
    setkeyword: (state, action: PayloadAction<string>) => {
      state.keyword = action.payload;
    },
    setPagination: (state, action: PayloadAction<IPagination>) => {
      state.pagination = action.payload;
    },
    setSearchPagination: (state, action: PayloadAction<IPagination>) => {
      state.searchPagination = action.payload;
    },
    setProductError: (state) => {
      state.hasErrors = true;
    },
    removeProductError: (state) => {
      state.hasErrors = false;
    },
    setSelectedProduct: (state, action: PayloadAction<IProduct>) => {
      state.selectedProduct = action.payload;
    },
    removeSelectedProduct: (state) => {
      state.selectedProduct = null;
    },
  },
});

export const {
  initLoading,
  finishLoading,
  setProducts,
  setProductError,
  removeProductError,
  setSelectedProduct,
  removeSelectedProduct,
  setReqSuccess,
  setPagination,
  setkeyword,
  setProductsPages,
  setcurrentProductsPerPage,
  setSearchPagination,
  setCurrentSearchResults,
  setProductSearchresults,
  setIsSearchResults,
  setTopProducts,
  setLastPageWithDelProd,
} = productSlice.actions;

export const getListProducts = (state: RootState) => state.products.products;
export const getProductsState = (state: RootState) => state.products;
export const getSelectedProduct = (state: RootState) =>
  state.products.selectedProduct;
export const getIsSearchResults = (state: RootState) =>
  state.products.isSearchResults;
export const getTopProducts = (state: RootState) => state.products.topProducts;

export default productSlice.reducer;
