import React, { FC, useEffect } from "react";
import { RouteComponentProps } from "react-router";
import { Row, Col } from "react-bootstrap";
import { useAppDispatch, useAppSelector } from "../app/hooks";
import { getCart } from "../features/Cart/cartSlice";
import { CheckoutSteps } from "../features/CheckoutSteps";
import {
  getCurrentOrder,
  getSuccess,
  setSuccess,
  getOrderState,
  isLoadingOrder,
} from "../features/MyOrders/MyOrdersSlice";
import {
  createOrder,
  getTotalCartPrice,
} from "../features/MyOrders/MyOrderAPI";
import { removeError } from "../features/ui/uiSlice";
import { PlaceOrderInfo } from "../features/MyOrders/PlaceOrderInfo";
import { OrderSummary } from "../features/MyOrders/OrderSummary";
import { isError, isLoading } from "../features/ui/uiSlice";
import { Loading } from "../components/loading/loading";
import { Message } from "../components/Message";

export const PlaceOrderScreen: FC<RouteComponentProps> = ({ history }) => {
  const cart = useAppSelector(getCart);
  const dispatch = useAppDispatch();
  const order = useAppSelector(getCurrentOrder);
  const success = useAppSelector(getSuccess);
  const orderState = useAppSelector(getOrderState);
  const error = useAppSelector(isError);
  const uiLoading = useAppSelector(isLoading);
  const loadingOrder = useAppSelector(isLoadingOrder);

  const placeOrderHandler = () => {
    dispatch(
      createOrder({
        orderItems: cart.items,
        subTotal: orderState.subtotal,
        shippingAddress: cart.shippingAddress,
        taxPrice: orderState.taxPrice,
        shippingPrice: orderState.shippingPrice,
        totalPrice: orderState.total,
        paymentMethod: cart.paymentMethod,
      })
    );
  };

  useEffect(() => {
    if (!loadingOrder && orderState.subtotal === 0 && !uiLoading && !error) {
      dispatch(getTotalCartPrice());
    }
  }, [loadingOrder, orderState.subtotal, dispatch, uiLoading, error]);

  useEffect(() => {
    if (success && order?._id) {
      history.push(`/order/${order._id}`);
    }
  }, [history, success, order?._id]);

  useEffect(() => {
    return () => {
      dispatch(removeError());
      dispatch(setSuccess(false));
    };
  }, [dispatch]);

  return (
    <div>
      <CheckoutSteps step1={true} step2={true} step3={true} step4={true} />
      <Row>
        <Col md={8}>
          <PlaceOrderInfo {...cart} />
        </Col>

        <Col md={4}>
          {error && <Message variant="danger">{error}</Message>}
          {loadingOrder ? (
            <Loading />
          ) : (
            <OrderSummary
              subtotal={orderState.subtotal}
              shippingPrice={orderState.shippingPrice}
              taxPrice={orderState.taxPrice}
              totalPrice={orderState.total}
              placeOrderHandler={placeOrderHandler}
            />
          )}
        </Col>
      </Row>
    </div>
  );
};
