import { AxiosResponse } from "axios";
import Swal from "sweetalert2";
import { AppThunk } from "../../app/store";

import { instance } from "../../app/axiosInstance";
import {
  startLoadingUsers,
  finishLoadingUsers,
  setUsersError,
  setUsers,
  setSuccess,
  setSelectedUser,
} from "./UsersSlice";
import { getMsgError } from "../../app/helpers";
import { IUserBase } from "../../schemas";

export const fetchAllUsers = (): AppThunk => async (dispatch, getState) => {
  const users = getState().users.users;

  if (users.length !== 0) return;

  dispatch(startLoadingUsers());
  try {
    const res: AxiosResponse = await instance.get("/api/users");

    dispatch(setUsers(res.data));
    dispatch(finishLoadingUsers());
  } catch (err: any) {
    dispatch(finishLoadingUsers());
    const msgError = getMsgError(err);
    dispatch(setUsersError(msgError));
    Swal.fire("Error", msgError, "error");
  }
};

export const userDelete =
  (id: string): AppThunk =>
  async (dispatch, getState) => {
    const users = getState().users.users;

    const userLocal = users.find((u) => u._id === id);
    if (!userLocal) {
      console.log(`USUARIO CON ID: ${id} NO ENCONTRADO EN MEMORIA`);
      return;
    }

    const updatedUsers = users.filter((u) => u._id !== userLocal._id);

    dispatch(startLoadingUsers());
    try {
      const res: AxiosResponse = await instance.delete(
        `/api/users/delete/${id}/`
      );

      console.log(res.data);
      dispatch(setUsers(updatedUsers));

      dispatch(finishLoadingUsers());
      dispatch(setSuccess(true));
    } catch (err: any) {
      dispatch(finishLoadingUsers());
      dispatch(setSuccess(false));
      const msgError = getMsgError(err);
      dispatch(setUsersError(msgError));
      Swal.fire("Error", msgError, "error");
    }
  };

export const userUpdate =
  (user: IUserBase): AppThunk =>
  async (dispatch, getState) => {
    const users = getState().users.users;

    const userLocal = users.find((u) => Number(u._id) === Number(user._id));
    if (!userLocal) {
      Swal.fire(
        "Error",
        `USUARIO CON ID: ${user._id} NO ENCONTRADO EN MEMORIA`,
        "error"
      );
      return;
    }

    dispatch(startLoadingUsers());
    try {
      const res: AxiosResponse = await instance.put(
        `/api/users/update/${user._id}/`,
        user
      );

      const updatedUsers = users.map((usr) => {
        if (Number(usr._id) === Number(user._id)) {
          return user;
        }
        return usr;
      });

      dispatch(setUsers(updatedUsers));
      dispatch(setSelectedUser(user));
      dispatch(finishLoadingUsers());
      dispatch(setSuccess(true));
    } catch (err: any) {
      dispatch(finishLoadingUsers());
      dispatch(setSuccess(false));
      const msgError = getMsgError(err);
      dispatch(setUsersError(msgError));
      Swal.fire("Error", msgError, "error");
    }
  };
