import React, { FC, useEffect } from "react";
import { Row, Col, Button } from "react-bootstrap";
import { RouteComponentProps } from "react-router";

import { useAppSelector, useAppDispatch } from "../../app/hooks";
import {
  getCurrentOrder,
  getOrderById,
  removeCurrentOrder,
} from "../../features/MyOrders/MyOrdersSlice";
import { Message } from "../../components/Message";
import { Loading } from "../../components/loading/loading";
import { isError, isLoading, removeError } from "../../features/ui/uiSlice";
import { OrderSummary } from "../../features/MyOrders/OrderSummary";
import { OrderDetails } from "../../features/MyOrders/OrderDetails";
import { deliverOrder } from "../../features/Orders/OrdersAPI";
import { getAuthState } from "../../features/Auth/authSlice";

interface MatchParams {
  id: string;
}

interface Props extends RouteComponentProps<MatchParams> {}

export const OrderScreen: FC<Props> = ({ match, history }) => {
  const id = match.params.id;
  const dispatch = useAppDispatch();
  const order = useAppSelector(getCurrentOrder);
  const error = useAppSelector(isError);
  const loading = useAppSelector(isLoading);
  const auth = useAppSelector(getAuthState);

  const { user } = auth;

  if (!order && !loading) {
    dispatch(getOrderById(id));
  }

  useEffect(() => {
    if (!user) {
      history.push("/login");
    }
    return () => {
      dispatch(removeError());
      dispatch(removeCurrentOrder());
    };
  }, [dispatch, history, user]);

  const orderDeliverHandler = () => {
    if (order) {
      dispatch(deliverOrder(order._id));
    }
  };

  console.log("order: ", order);
  return (
    <div>
      <Row>
        <Col md={8}>
          {error && <Message variant="danger">{error}</Message>}
          {loading && <Loading />}
          {order && <OrderDetails {...order} />}
        </Col>
        <Col md={4} className="mt-4">
          {order?.subTotal ? (
            <OrderSummary
              subtotal={order.subTotal}
              shippingPrice={order.shippingPrice}
              taxPrice={order.taxPrice}
              totalPrice={order.totalPrice}
            />
          ) : undefined}
          {user?.isAdmin && order?.isPaid ? (
            <Button
              type="button"
              variant="success"
              className="mt-3 btn btn-block"
              onClick={orderDeliverHandler}
              disabled={order && order.isDelivered ? true : false}
            >
              {order && order.isDelivered ? "Delivered" : "Mark as delivered"}
            </Button>
          ) : undefined}
        </Col>
      </Row>
    </div>
  );
};
