import React, { FC, useEffect, useState } from "react";
import { RouteComponentProps } from "react-router";
import { Form, Button, Row, Col, Table } from "react-bootstrap";
import { LinkContainer } from "react-router-bootstrap";
import { useAppDispatch, useAppSelector } from "../app/hooks";
import { useForm } from "../app/hooks/useForm";
import { Message } from "../components/Message";
import { isChecking, getUser } from "../features/Auth/authSlice";
import { isError, removeError } from "../features/ui/uiSlice";
import { updateUserProfile } from "../features/Auth/authAPI";
import { LoadSpinner } from "../components/LoadSpinner";
import {
  getUserOrders,
  refreshUserOrders,
} from "../features/MyOrders/MyOrderAPI";
import {
  getOrders,
  isLoadingOrder,
  getOrderError,
  setCurrentOrder,
} from "../features/MyOrders/MyOrdersSlice";
import { Loading } from "../components/loading/loading";
import { IOrderInDetails } from "../schemas";

export const ProfileScreen: FC<RouteComponentProps> = ({ history }) => {
  const [message, setMessage] = useState<string | undefined>(undefined);

  const dispatch = useAppDispatch();
  const checking = useAppSelector(isChecking);
  const orders = useAppSelector(getOrders);
  const loadingOrders = useAppSelector(isLoadingOrder);
  const error = useAppSelector(isError);
  const orderError = useAppSelector(getOrderError);
  const user = useAppSelector(getUser);
  //   const redirect = location.search ? location.search.split("=")[1] : "/";

  interface formProps {
    name: string;
    email: string;
    password: string;
    confirmPassword: string;
  }

  const [formValues, actions] = useForm<formProps>({
    name: user?.name || "",
    email: user?.email || "",
    password: "",
    confirmPassword: "",
  });

  const {
    values: { name, email, password, confirmPassword },
  } = formValues;
  const { handleInputChange } = actions;

  const submitHandler = (e: React.FormEvent) => {
    e.preventDefault();

    if (password !== confirmPassword) {
      setMessage("Passwords do not match");
    } else {
      setMessage("");
      dispatch(updateUserProfile(name, email, password));
    }
  };

  useEffect(() => {
    console.log("EJECUTANDO getUserOrders desde ProfileScreen");
    dispatch(getUserOrders());
  }, [dispatch]);

  useEffect(() => {
    return () => {
      dispatch(removeError());
    };
  }, [dispatch]);

  useEffect(() => {
    if (!user) {
      history.push("/login");
    }
  }, [history, user]);

  const setCurrentOrderHandler = (order: IOrderInDetails) => {
    dispatch(setCurrentOrder(order));
  };
  const refreshUserOrdersHandler = () => {
    dispatch(refreshUserOrders());
  };
  return (
    <>
      <Row className="text-center mt-3">
        <Col>
          <h1>User Profile</h1>
        </Col>
      </Row>
      <Row className="mt-1">
        <Col md={3}>
          <h2 className=" text-center">User Info</h2>
          {message && <Message variant="danger">{message}</Message>}
          {error && <Message variant="danger">{error}</Message>}
          <Form onSubmit={submitHandler}>
            <Form.Group controlId="name">
              <Form.Label>Name</Form.Label>
              <Form.Control
                required
                type="text"
                placeholder="Enter your name"
                name="name"
                value={name}
                onChange={handleInputChange}
              ></Form.Control>
            </Form.Group>
            <Form.Group controlId="email">
              <Form.Label>Email Address</Form.Label>
              <Form.Control
                required
                type="email"
                placeholder="Enter Email"
                name="email"
                value={email}
                onChange={handleInputChange}
              ></Form.Control>
            </Form.Group>
            <Form.Group controlId="password">
              <Form.Label>Password</Form.Label>
              <Form.Control
                type="password"
                placeholder="Enter Password"
                name="password"
                value={password}
                onChange={handleInputChange}
              ></Form.Control>
            </Form.Group>
            <Form.Group controlId="confirmpassword">
              <Form.Label>confirm Password</Form.Label>
              <Form.Control
                type="password"
                placeholder="Enter Password"
                name="confirmPassword"
                value={confirmPassword}
                onChange={handleInputChange}
              ></Form.Control>
            </Form.Group>
            <Button
              type="submit"
              variant="primary"
              className="mt-2"
              disabled={checking}
            >
              {checking ? "loading" : "Update"}
            </Button>
            {checking ? <LoadSpinner /> : undefined}
          </Form>
        </Col>
        <Col md={9}>
          <h2 className=" text-center">My Orders</h2>
          {loadingOrders && <Loading />}
          {orderError && <Message variant="danger">{orderError}</Message>}
          {orders.length > 0 ? (
            <Table striped responsive className="table-sm">
              <thead>
                <tr>
                  <th>ID</th>
                  <th>Date</th>
                  <th>Total</th>
                  <th>Paid</th>
                  <th>Delivered</th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {orders.map((order) => (
                  <tr key={order._id}>
                    <td>{order._id}</td>
                    <td>{order.createdAt.substring(0, 10)}</td>
                    <td>${order.totalPrice}</td>
                    <td>
                      {order.isPaid ? (
                        order.paidAt.substring(0, 10)
                      ) : (
                        <i
                          className="fas fa-times"
                          style={{ color: "red" }}
                        ></i>
                      )}
                    </td>
                    <td>
                      <LinkContainer to={`/order/${order._id}`}>
                        <Button
                          className="btn-sm"
                          onClick={() => setCurrentOrderHandler(order)}
                        >
                          Details
                        </Button>
                      </LinkContainer>
                    </td>
                    <td></td>
                  </tr>
                ))}
              </tbody>
            </Table>
          ) : undefined}
          <Button className="btn-sm" onClick={refreshUserOrdersHandler}>
            Update user Orders
          </Button>
        </Col>
      </Row>
    </>
  );
};
