import { createSlice, PayloadAction, createAsyncThunk } from "@reduxjs/toolkit";
import { RootState } from "../../app/store";
import { IUser } from "../../schemas";
import { resetOrders } from "../MyOrders/MyOrdersSlice";
import { resetUsers } from "../Users/UsersSlice";
import { removeError } from "../ui/uiSlice";

interface authState {
  isLoggedIn: boolean;
  checking: boolean;
  user: IUser | undefined;
}

const initialState: authState = {
  isLoggedIn: false,
  checking: false,
  user: undefined,
};

export const startLogout = createAsyncThunk(
  "auth/logout",
  (_, { dispatch }) => {
    dispatch(resetOrders());
    dispatch(resetUsers());
    dispatch(removeError());
  }
);

export const authSlice = createSlice({
  name: "auth",
  initialState,
  reducers: {
    login: (state, action: PayloadAction<IUser>) => {
      state.isLoggedIn = true;
      state.user = action.payload;
    },
    logout: (state) => {
      state.isLoggedIn = false;
      state.checking = false;
      state.user = undefined;
    },
    initLogin: (state) => {
      state.checking = true;
    },
    finishLogin: (state) => {
      state.checking = false;
    },
  },
  extraReducers: (builder) => {
    builder.addCase(startLogout.fulfilled, (state) => {
      state.user = undefined;
      state.checking = false;
      state.isLoggedIn = false;
      localStorage.removeItem("user");
    });
  },
});

export const { login, logout, initLogin, finishLogin } = authSlice.actions;

export const isChecking = (state: RootState) => state.auth.checking;
export const getUser = (state: RootState) => state.auth.user;
export const isAuthenticated = (state: RootState) => state.auth.isLoggedIn;
export const getAuthState = (state: RootState) => state.auth;

export default authSlice.reducer;
