import axios from "axios";
import { loadFromLocalStorage } from "./helpers";

//const base_url = "proshop.ibazaldua.net";

axios.defaults.baseURL = "https://proshop.ibazaldua.net";

export const instance = axios.create({
  //baseURL: base_url,
  headers: {
    "Content-Type": "application/json",
    Accept: "application/json",
  },
});

export const instanceImageUpload = axios.create({
  //baseURL: base_url,
  headers: {
    "Content-Type": "multipart/form-data",
  },
});

instance.interceptors.request.use(
  (config) => {
    const userFromLS = loadFromLocalStorage("user");
    if (userFromLS) {
      const token = userFromLS.token;
      config.headers["Authorization"] = `Bearer ${token}`;
      return config;
    }
    return config;
  },
  (error) => {
    Promise.reject(error);
  }
);

instanceImageUpload.interceptors.request.use(
  (config) => {
    const userFromLS = loadFromLocalStorage("user");
    if (userFromLS) {
      const token = userFromLS.token;
      config.headers["Authorization"] = `Bearer ${token}`;
      return config;
    }
    return config;
  },
  (error) => {
    Promise.reject(error);
  }
);
