import React, { VFC, useState, useEffect } from "react";
import { Row, Col, ListGroup, Button, Card, Carousel } from "react-bootstrap";
import { Link, useHistory } from "react-router-dom";

import { IProduct } from "../../schemas";
import { Rating } from "./Rating";
import { CountInStock2 } from "./CountInStock2";
import { addToCartLS } from "../Cart/cartSlice";
import { useAppDispatch, useAppSelector } from "../../app/hooks";
import { removeSelectedProduct } from "./productSlice";
import {
  getLoadingReviews,
  getSelectedProductReviews,
  getLoadingReview,
  setCreateReviewSuccess,
} from "../Reviews/ReviewsSlice";
import { getProductReviews } from "../Reviews/ReviewsAPI";
import { Loading } from "../../components/loading/loading";
import { ProductReviews } from "../../components/products/ProductReviews";
import { Message } from "../../components/Message";

import { WriteReview } from "../../components/Review/WriteReview";

export const ProductDetails: VFC<IProduct> = (product: IProduct) => {
  const [qty, setQty] = useState<number>(1);

  const dispatch = useAppDispatch();
  const isProductReviewsLoading = useAppSelector(getLoadingReviews);
  const isProductReviewLoading = useAppSelector(getLoadingReview);
  const selectedProductReviews = useAppSelector(getSelectedProductReviews);

  const history = useHistory();

  useEffect(() => {
    if (
      !selectedProductReviews ||
      (selectedProductReviews &&
        Number(selectedProductReviews[0].product) !== Number(product._id))
    ) {
      dispatch(getProductReviews(product._id));
    }

    return () => {
      dispatch(removeSelectedProduct());
      // dispatch(setSelectedProductReviews(undefined));
      dispatch(setCreateReviewSuccess(false));
    };
  }, [dispatch, selectedProductReviews, product._id]);

  const updateQty = (e: React.ChangeEvent<HTMLInputElement>) => {
    const newValue = e.target.value;
    setQty(Number(newValue));
  };

  const addToCartHandler = () => {
    const item = {
      product: product,
      qty: qty,
    };
    dispatch(addToCartLS(item));
    history.push(`/cart/${product._id}?qty=${qty}`);
  };

  return (
    <>
      <Row className="justify-content-center">
        <Col md={4}>
          {product.images ? (
            <Carousel pause="hover">
              {product.images.map((image) => {
                return (
                  <Carousel.Item interval={3000} key={image.url}>
                    <img
                      className="img-fluid "
                      src={image.url}
                      alt={product.name}
                    />
                  </Carousel.Item>
                );
              })}
            </Carousel>
          ) : null}
        </Col>
        <Col md={4}>
          <ListGroup variant="flush">
            <ListGroup.Item>
              <h3>{product.name}</h3>
            </ListGroup.Item>
            <ListGroup.Item>
              <Rating
                value={product.rating}
                text={`${product.numReviews} reviews`}
                color={"#f8e825"}
              />
            </ListGroup.Item>
            <ListGroup.Item>Price: ${product.price}</ListGroup.Item>
            <ListGroup.Item>Description: ${product.description}</ListGroup.Item>
          </ListGroup>
        </Col>
        <Col md={4}>
          <Card>
            <ListGroup variant="flush">
              <ListGroup.Item>
                <Row>
                  <Col>Price:</Col>
                  <Col>
                    <strong>${product.price}</strong>
                  </Col>
                </Row>
              </ListGroup.Item>
              <ListGroup.Item>
                <Row>
                  <Col>Status:</Col>
                  <Col>
                    {product.countInStock > 0 ? "In Stock" : "Out of Stock"}
                  </Col>
                </Row>
              </ListGroup.Item>

              {product.countInStock > 0 && (
                <ListGroup.Item>
                  <Row>
                    <Col>Qty</Col>
                    <Col xs="auto">
                      <CountInStock2
                        qty={qty}
                        countInStock={product.countInStock}
                        updateQty={updateQty}
                      />
                    </Col>
                  </Row>
                </ListGroup.Item>
              )}

              <ListGroup.Item>
                <Button
                  className="btn-block"
                  type="button"
                  disabled={product.countInStock === 0}
                  onClick={addToCartHandler}
                >
                  ADD TO CART
                </Button>
              </ListGroup.Item>
            </ListGroup>
          </Card>
        </Col>
      </Row>
      <Row>
        <Col md={6}>
          <h3>Reviews</h3>
          {isProductReviewsLoading ? (
            <Loading />
          ) : selectedProductReviews && selectedProductReviews.length > 0 ? (
            <ProductReviews
              reviews={[...selectedProductReviews]}
              numReviews={product.numReviews}
            />
          ) : (
            <>
              <Message variant="info">No Reviews</Message>
            </>
          )}
        </Col>
        <Col md={6}>
          {isProductReviewLoading && <Loading />}
          <WriteReview productId={product._id} />
        </Col>
      </Row>
    </>
  );
};
