import React, { VFC } from "react";
import { Button, Row, Col, ListGroup, Card, Spinner } from "react-bootstrap";
import { Message } from "../../components/Message";
import { isError, isLoading } from "../ui/uiSlice";
import { useAppSelector } from "../../app/hooks";

interface Props {
  subtotal: number;
  shippingPrice: number;
  taxPrice: number;
  totalPrice: number;
  placeOrderHandler?: () => void;
}

export const OrderSummary: VFC<Props> = ({
  subtotal,
  shippingPrice,
  taxPrice,
  totalPrice,
  placeOrderHandler,
}) => {
  const error = useAppSelector(isError);
  const loading = useAppSelector(isLoading);
  return (
    <Card>
      <ListGroup variant="flush">
        <ListGroup.Item>
          <h2>Order Summary</h2>
        </ListGroup.Item>
        <ListGroup.Item>
          <Row>
            <Col>Subtotal:</Col>
            <Col>${subtotal}</Col>
          </Row>
        </ListGroup.Item>
        <ListGroup.Item>
          <Row>
            <Col>Shipping:</Col>
            <Col>${shippingPrice}</Col>
          </Row>
        </ListGroup.Item>
        <ListGroup.Item>
          <Row>
            <Col>Tax:</Col>
            <Col>${taxPrice}</Col>
          </Row>
        </ListGroup.Item>
        <ListGroup.Item>
          <Row>
            <Col>Total:</Col>
            <Col>${totalPrice}</Col>
          </Row>
        </ListGroup.Item>
        {error && <Message variant="danger">{error}</Message>}
        <ListGroup.Item></ListGroup.Item>
        {placeOrderHandler ? (
          <ListGroup.Item>
            <Button
              type="button"
              className="btn-block"
              variant="primary"
              onClick={placeOrderHandler}
              disabled={loading || error ? true : false}
            >
              {loading
                ? (
                    <Spinner
                      as="span"
                      size="sm"
                      role="status"
                      aria-hidden="true"
                      animation="border"
                    />
                  ) + "Loading ..."
                : "Place Order"}
            </Button>
          </ListGroup.Item>
        ) : undefined}
      </ListGroup>
    </Card>
  );
};
