import React from "react";
import { Container } from "react-bootstrap";

import { HashRouter as Router, Switch, Route } from "react-router-dom";
import { Footer } from "../components/Footer";
import Header from "../components/Header";
import { HomeScreen } from "../screens/HomeScreen";
import { ProductScreen } from "../screens/ProductScreen";

import { CartScreen } from "../screens/CartScreen";
import { LoginScreen } from "../screens/LoginScreen";
import { RegisterScreen } from "../screens/RegisterScreen";
import { ProfileScreen } from "../screens/ProfileScreen";
import { ShippingScreen } from "../screens/ShippingScreen";
import { PaymentScreen } from "../screens/PaymentScreen";
import { PlaceOrderScreen } from "../screens/PlaceOrderScreen";
import { OrderScreen } from "../screens/Order/OrderScreen";
import { UserListScreen } from "../screens/UserListScreen";
import EditUserScreen from "../screens/UserEditScreen";
import ProductListScreen from "../screens/ProductListCreen";
import ProductEditScreen from "../screens/ProductEditScreen";
import ProductCreateScreen from "../screens/ProductCreateScreen";
import ProtectedRoute, { ProtectedRouteProps } from "./PrivateRoute";
import { useAppSelector } from "../app/hooks";
import { isAuthenticated } from "../features/Auth/authSlice";
import { OrderListScreen } from "../screens/Order/OrderListScreen";

export const AppRouter = () => {
  const isLoggedIn = useAppSelector(isAuthenticated);

  const defaultProtectedRouteProps: ProtectedRouteProps = {
    isAuthenticated: isLoggedIn,
    authenticationPath: "/login",
  };

  return (
    <Router>
      <div>
        <Header />
        <main>
          <Container>
            <Switch>
              <Route path="/" component={HomeScreen} exact />
              <Route path="/login" component={LoginScreen} />
              <Route path="/register" component={RegisterScreen} />
              <Route path="/profile" component={ProfileScreen} />
              <Route path="/shipping" component={ShippingScreen} />
              <Route path="/placeorder" component={PlaceOrderScreen} />
              <Route path="/payment" component={PaymentScreen} />
              <ProtectedRoute
                {...defaultProtectedRouteProps}
                path="/admin/users"
                component={UserListScreen}
              />
              <ProtectedRoute
                {...defaultProtectedRouteProps}
                path="/admin/user/:id/edit"
                component={EditUserScreen}
              />
              <ProtectedRoute
                {...defaultProtectedRouteProps}
                path="/admin/productList"
                component={ProductListScreen}
              />
              <ProtectedRoute
                {...defaultProtectedRouteProps}
                path="/admin/orders"
                component={OrderListScreen}
              />
              <ProtectedRoute
                {...defaultProtectedRouteProps}
                path="/admin/product/create"
                component={ProductCreateScreen}
              />
              <ProtectedRoute
                {...defaultProtectedRouteProps}
                path="/admin/product/:id/edit"
                component={ProductEditScreen}
              />
              <Route path="/product/:id" component={ProductScreen} />
              <Route path="/cart/:id?" component={CartScreen} />
              <Route path="/order/:id" component={OrderScreen} />
            </Switch>
          </Container>
        </main>
        <Footer />
      </div>
    </Router>
  );
};
