import React, { FC, useEffect, useState } from "react";
import { RouteComponentProps } from "react-router";
import { Link } from "react-router-dom";
import { Form, Button, Container } from "react-bootstrap";

import { useAppDispatch, useAppSelector } from "../app/hooks";
import { useForm } from "../app/hooks/useForm";

import { Message } from "../components/Message";

import {
  setSelectedUserById,
  getSelectedUser,
  removeSelectedUser,
  isLoadingUsers,
  getErrorUsers,
  getUsersSuccess,
  removeUsersError,
} from "../features/Users/UsersSlice";

import { userUpdate } from "../features/Users/UsersAPI";

import { LoadSpinner } from "../components/LoadSpinner";
import { IUserBase } from "../schemas";

interface MatchParams {
  id: string;
}

interface Props extends RouteComponentProps<MatchParams> {}

const EditUserScreen: FC<Props> = ({ match, history }) => {
  const userId = match.params.id;

  const dispatch = useAppDispatch();
  const loading = useAppSelector(isLoadingUsers);
  const error = useAppSelector(getErrorUsers);
  const user = useAppSelector(getSelectedUser);
  const success = useAppSelector(getUsersSuccess);

  const [isAdmin, setIsAdmin] = useState<boolean>(user?.isAdmin || false);

  interface formProps {
    name: string;
    email: string;
  }

  const [formValues, actions] = useForm<formProps>({
    name: user?.name || "",
    email: user?.email || "",
  });

  const {
    values: { name, email },
  } = formValues;

  const { handleInputChange } = actions;

  const submitHandler = (e: React.FormEvent) => {
    e.preventDefault();

    const updatedUser: IUserBase = {
      _id: userId,
      name,
      email,
      isAdmin,
    };

    dispatch(userUpdate(updatedUser));
  };

  useEffect(() => {
    if (!user?.name || user?._id !== userId) {
      dispatch(setSelectedUserById(userId));
    }
  }, [user, userId, dispatch]);

  useEffect(() => {
    if (!error && !loading && success) {
      history.push("/admin/users");
    }
  }, [error, loading, history, success]);

  useEffect(() => {
    return () => {
      dispatch(removeUsersError());
      dispatch(removeSelectedUser());
    };
  }, [dispatch]);

  return (
    <Container>
      <Link to="/admin/users">Go Back</Link>
      <h1>Edit User</h1>
      {loading ? (
        <LoadSpinner />
      ) : error ? (
        <Message variant="danger">{error}</Message>
      ) : (
        <Form onSubmit={submitHandler}>
          <Form.Group controlId="name">
            <Form.Label>Name</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter your name"
              name="name"
              value={name}
              onChange={handleInputChange}
            ></Form.Control>
          </Form.Group>
          <Form.Group controlId="email">
            <Form.Label>Email Address</Form.Label>
            <Form.Control
              type="email"
              placeholder="Enter Email"
              name="email"
              value={email}
              onChange={handleInputChange}
            ></Form.Control>
          </Form.Group>
          <Form.Group controlId="isadmin">
            <Form.Check
              type="checkbox"
              label="Is Admin"
              checked={isAdmin}
              onChange={(e) => setIsAdmin(e.target.checked)}
            ></Form.Check>
          </Form.Group>
          <Button
            type="submit"
            variant="primary"
            className="mt-2"
            disabled={loading}
          >
            Update
          </Button>
          {loading ? <LoadSpinner /> : undefined}
        </Form>
      )}
    </Container>
  );
};

export default EditUserScreen;
