import { FC, useEffect } from "react";
import { Container } from "react-bootstrap";
import { RouteComponentProps } from "react-router";
import { Link } from "react-router-dom";
import { useAppDispatch, useAppSelector } from "../app/hooks";
import { LoadSpinner } from "../components/LoadSpinner";
import ProductForm from "../features/products/ProductForm";
import { Message } from "../components/Message";
import {
  getProductsState,
  setSelectedProductById,
  setReqSuccess,
} from "../features/products/productSlice";
import { updateProduct } from "../features/products/productAPI";
import { IProduct, ICreateProduct, IImage } from "../schemas";

interface MatchParams {
  id: string;
}

interface Props extends RouteComponentProps<MatchParams> {}

const ProductEditScreen: FC<Props> = ({ match, history }) => {
  const productId = match.params.id;

  const dispatch = useAppDispatch();
  const {
    loading,
    hasErrors: error,
    selectedProduct,
    reqSuccess,
  } = useAppSelector(getProductsState);

  useEffect(() => {
    if (!loading && !error && !selectedProduct) {
      dispatch(setSelectedProductById(productId));
    }
  }, [loading, error, selectedProduct, dispatch, productId]);

  useEffect(() => {
    if (reqSuccess) {
      history.push("/admin/productList");
    }
    return () => {
      dispatch(setReqSuccess(false));
    };
  }, [reqSuccess, history, dispatch]);

  // useEffect(() => {
  //   return () => {dispatch(setReqSuccess(false))};
  // }, []);

  const submitHandler = (
    product: IProduct | ICreateProduct,
    images?: FileList,
    imagesToDelete?: IImage[]
  ) => {
    dispatch(updateProduct(product as IProduct, images, imagesToDelete));
  };

  return (
    <Container>
      <Link to="/admin/productList">Go Back</Link>
      <h1>Edit Product</h1>
      {loading ? (
        <LoadSpinner />
      ) : error ? (
        <Message variant="danger">{error}</Message>
      ) : (
        <ProductForm product={selectedProduct} eventHandler={submitHandler} />
      )}
    </Container>
  );
};

export default ProductEditScreen;
